<template>
	<div class="user">
		<div class="searchDiv">


      <el-form  label-width="150px" >
        <el-form-item label="请选择人员">
          			<span>
                 <el-cascader
                   v-model="casValue1"
                   :options="options1"
                   @change="handleChange2">
                </el-cascader>

			        </span>
        </el-form-item>
        <el-form-item label="请选择上传分类">

          <span>
            <el-cascader
              v-model="casValue"
              :options="options"
              @change="handleChange"></el-cascader>
          </span>
        </el-form-item>
        <el-form-item label="请填写具体数值">
          <span>
            <el-input v-model="number" style="width:200px" placeholder="请输入数据"></el-input>
          </span>
        </el-form-item>
        <el-form-item label="请上传图片">
         	<span>

				 <el-upload
           action="http://106.15.238.244:3387/admin_uploadShopImg"
           list-type="picture-card"
           :on-success="success"

           :show-file-list="false"
           :data="imgData"
           :limit="1" >
				   <img width="100%" height="150"  :src="this.baseUrl +'/public'+img" alt="" v-if="img">
				   <i v-else class="el-icon-plus avatar-uploader-icon"></i>
				 </el-upload>

			</span>
        </el-form-item>
      </el-form>
      <div style="width:100px;margin:auto">
        <el-button style="margin:auto" type="success" plain @click="uploadFile()">确定上传</el-button>

      </div>

		</div>



  </div>
</template>

<script>
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')
			this.search()
		},
		data() {
			return {
				options1:[],
				casValue1: [],
				dialogImageUrl: '',
				dialogVisible: false,
				clientSize: true,
				selects: [],
				formData:{},
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				imgData:{
					adminName: sessionStorage.getItem('admin'),
					keys: sessionStorage.getItem('keys')
				},
				timeArr: null,
				keyword: "",
				type: "",
				state: "",
				class: '',
				grade: '',
				weight:'',
				mark:'',
				img:'',
				number:'',
				numbers:'',//个数
				page: 1,
				pageSize: 10,
				typeVal: '纸质垃圾',
				gradeVal: '全部',
				classVal: '全部',
				classList: [{
					value: '全部',
					label: '全部'
				}, {
					value: '1班',
					label: '1班'
				}, {
					value: '2班',
					label: '2班'
				}, {
					value: '3班',
					label: '3班'
				}, {
					value: '4班',
					label: '4班'
				}],
				gradeList: [{
					value: '全部',
					label: '全部'
				}, {
					value: '预备年级',
					label: '预备年级'
				}, {
					value: '七年级',
					label: '七年级'
				}, {
					value: '八年级',
					label: '八年级'
				}, {
					value: '九年级',
					label: '九年级'
				}],
				typeList: [{
					value: '纸质垃圾',
					label: '纸质垃圾'
				}, {
					value: '塑料瓶罐',
					label: '塑料瓶罐' //
				}, {
					value: '垃圾入箱',
					label: '垃圾入箱'
				}, {
					value: '垃圾分类',
					label: '垃圾分类'
				}, {
					value: '二次纸再利用',
					label: '二次纸再利用'
				}, {
					value: '垃圾桶清洁',
					label: '垃圾桶清洁'
				}, {
					value: '图片公示',
					label: '图片公示'
				}],
				list: [

				],
				tableData: [],
				listTotal: 0,
				exportLoading: false,
				flykAll: 0,
				ethAll: 0,
				ethService: 0,
				flykService: 0,
				ethOk: 0,
				flykOk: 0,
				centerDialogVisible: false,
				scopeRow: {},

				checkLoading: false,
				value:'',
				casValue:[],
				options:[{
					value: '纸质垃圾',
					label: '纸质垃圾',
					children:[{
						value:'重量(g)',
						label:'重量(g)'
					}]
				},{
					value: '塑料瓶罐',
					label: '塑料瓶罐',
					children:[{
						value:'重量(g)',
						label:'重量(g)'
					}]
				},{
					value: '垃圾入箱',
					label: '垃圾入箱',
					children:[{
						value:'个',
						label:'个'
					}]
				}, {
					value: '垃圾分类',
					label: '垃圾分类',
					children:[{
						value:'个',
						label:'个'
					}]
				},{
					value: '二次纸再利用',
					label: '二次纸再利用',
					children:[{
						value:'本',
						label:'本'
					}]
				}, {
					value: '图片公示',
					label: '图片公示',
					children:[{
						value:'分',
						label:'分'
					}]
				}, {
          value: '活动加分',
          label: '活动加分',
          children:[{
            value:'分',
            label:'分'
          }]
        }]
			};
		},
		methods: {
			success(res){
				console.log(res);
				console.log(res.filePath[0]);
				this.img = res.filePath[0];
			},
			search() {
				const _this = this;
				let data = {
					adminName: this.admin,
					keys: this.keys,
				};
				this.axios.post(this.baseUrl + "/admin_cascaderUserList", data)
				.then(function(response) {
					if (response.data.result == 200) {
						_this.options1 = response.data.list;
						_this.$toast.message('班级人员信息已加载！');
					} else {
						_this.$toast.message(response.data.data);
					}

				})
				.catch(function(error) {
					_this.$toast.message('网路错误！');
					console.log(error);
					_this.checkLoading = false

				});
			},

			beforeAvatarUpload(){

			},
			uploadOnChange(data){
				console.log("uploadOnChange",data)
			},
			uploadSucess(data){
				console.log("uploadSucess",data)
			},
			uploadError(data){
				console.log("uploadError",data)
			},
			handleChanges(file){
				 var _this = this;
				if (file.raw) {
				    let reader = new FileReader() ;
				    reader.onload = function (e) {
				       _this.contentHtml = e.target.result;
				    };
				    reader.readAsText(file.raw,'gb2312');
				  this.img = file.url;
				}
			},
			 handleChange(value) {
        console.log(value)
				this.type = value[0];
				this.state=value[1]

			 },

			 handleChange2(value){
			 	console.log(value)
			 	this.pointer = value[2];
			 },
			action(){
				return this.baseUrl+'admin_addUpdateClassCoin';
			},
			submitUpload() {
				this.$refs.upload.submit();

			},
			uploadFile(){
				//this.$refs.upload.submit();

				const _this = this;

        console.log(this.pointer)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					_number:this.number,
					_type: this.type,
					_img:this.img,
					_id: this.casValue1[2],
					_state: this.state,
				};
console.log(data)
				this.axios.post(this.baseUrl + "/admin_addUpdateClassCoin", data )
					.then(function(response) {
						_this.$toast.message(response.data.data);
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});

			},

			handlePreview(file) {
				console.log(file);
			},







		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



