<template>
	<div class="user">
		<div class="searchDiv">

			<span>
				<el-select v-model="gradeVal" placeholder="请选择年级" >
					<el-option v-for="(item,index) in gradeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>

			<span>
				<el-select v-model="classVal" placeholder="请选择班级"   @change="changeFn">
					<el-option v-for="(item,index) in classList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>

			<span class="inputSpan">
				<el-input :placeholder="placeholder" v-model.number="keyword" ></el-input>
			</span>


			<span class="searchButtonDiv">

				<el-button type="primary" @click="search()">
					一键生成
				</el-button>


			</span>
		</div>


	</div>
</template>

<script>
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			};
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')
			console.log(this.admin, this.keys);
		},
		data() {
			return {
			  code:'',
				clientSize: true,
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				keyword: "",
				type: "",
				state: "",
				class: '',
				grade: '',
				typeVal: '_name',
				gradeVal: '预备年级',
				classVal: '1班',
				classList: [ {
					value: '1班',
					label: '1班'
				}, {
					value: '2班',
					label: '2班'
				}, {
					value: '3班',
					label: '3班'
				}, {
					value: '4班',
					label: '4班'
				}, {
					value: '老师班',
					label: '老师班'
				}],
				gradeList: [{
					value: '预备年级',
					label: '预备年级'
				}, {
					value: '七年级',
					label: '七年级'
				}, {
					value: '八年级',
					label: '八年级'
				}, {
					value: '九年级',
					label: '九年级'
				}],
				typeList: [{
					value: '_name',
					label: '姓名'
				}, {
					value: '_acc',
					label: '账号'
				}, {
					value: '_id',
					label: '学生id'
				}, {
					value: '_batch',
					label: '索引码'
				}],
				list: [

				],
				placeholder:'请输入学生数量',
			
			};
		},
		mounted() {
			//this.initdata();
		},
		methods: {
			changeFn(val){
				console.log('val',val);
				val=='老师班'?(this.placeholder='请输入老师数量') : (this.placeholder='请输入学生数量')
			},
			initdata() {
				const _this = this;
				let data = {
					adminName: this.admin,
					keys: this.keys,
					_number: this.keyword,
					_class: this.classVal,
					_grade: this.gradeVal,
				};





			 	if(!this.keyword){
					_this.$toast.message('学生数量必填');
					return;
				}

				this.axios.post(this.baseUrl + "/oneKeyRegisterUser", data)
					.then(function(response) {
						if (response.data.result == 200) {
							_this.$toast.message(`生成成功！查询索引码为 ${response.data.code}`);
							_this.code=response.data.code

						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},
			search() {
				this.initdata();
			},

		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



