<template>
  <div class="loginEl">
      <mu-scale-transition>
    <div class="login" v-show="isShow">

        <div class="loginDiv">
          <div class="loginTitle"><img src="../../assets/admin_av.png" alt="" class="logo"></div>
          <div class="tittle">五四中学低碳环保管理系统</div>
          <mu-form ref="form" :model="validateForm" class="mu-demo-form" >
            <!-- <mu-form-item  prop="username" :rules="usernameRules">
            <mu-text-field action-icon="account_circle" v-model="validateForm.username" prop="username"></mu-text-field>
            </mu-form-item> -->
						<mu-form-item   prop="phone" :rules="phoneRules">
						    <mu-text-field type="phone" action-icon="stay_current_portrait"  v-model="validateForm.phone" prop="phone"></mu-text-field>
						</mu-form-item>
            <mu-form-item   prop="password" :rules="passwordRules">
                <mu-text-field  :action-icon="visibility ? 'visibility' : 'visibility_off'" :action-click="() => (visibility = !visibility)" :type="visibility ? 'text' : 'password'"   v-model="validateForm.password" prop="password"></mu-text-field>
            </mu-form-item>

             <!-- <mu-form-item   prop="code" :rules="codeRules">
                <mu-text-field type="code"  action-icon="info"  v-model="validateForm.code" prop="code"></mu-text-field>
            </mu-form-item> -->

            <mu-form-item>
                <!-- <div class="buttonDiv1">
                    <mu-button full-width round color="red" :disabled="codeDisabled" id="codeB" v-loading="codeLoading" @click="getCode">Get Code</mu-button>
                </div>   -->
                <div class="buttonDiv2">
                <mu-scale-transition>

                    <mu-button v-show="!isSubmit" full-width round id="loginB" :disabled="loginDisabled" @click="submit" color="primary" style="background:#90c434;border-radius: 5px;">登录</mu-button>
                </mu-scale-transition>
                <mu-scale-transition>

                     <mu-button v-show="isSubmit" small fab id="loginB" :disabled="loginDisabled" color="rgb(144, 196, 52)">
                  <div class="loginCircle circle1"></div>
                  <div class="loginCircle circle2"></div>
                  <div class="loginCircle circle3"></div>
                </mu-button>
                </mu-scale-transition>

                </div>
            </mu-form-item>
        </mu-form>

        </div>

    </div>
  </mu-scale-transition>

  </div>
</template>

<script>
export default {
  name: 'loginEl',
  components:{

  },
  mounted(){
    let admin=sessionStorage.getItem('admin')
    if(admin){
      this.isShow=false
    }else{
      this.isShow=true

    }
  },
  data () {
    return {
        isShow:false,
        visibility: true,
        usernameRules: [
        { validate: (val) => !!val, message: '必须填写用户名'},
        { validate: (val) => val.length >= 1, message: '用户名长度大于3'}
      ],
      passwordRules: [
        { validate: (val) => !!val, message: '必须填写密码'},
        { validate: (val) => val.length >= 6 && val.length <= 24, message: '密码长度大于6小于24'}
      ],
      phoneRules: [
        { validate: (val) => !!val, message: '必须填写手机号'},
        { validate: (val) => val.length==11 && !isNaN(val), message: '手机号长度为11位且为数字'}
      ],
      // codeRules: [
      //   { validate: (val) => !!val, message: '必须填写验证码'},
      //   { validate: (val) => val.length>=4 && val.length<=6 && !isNaN(val), message: '验证码为长度为4～6位且为数字'}
      // ],
      argeeRules: [{ validate: (val) => !!val, message: '必须同意用户协议'}],
      validateForm: {
        username: '',
        password: '',
        phone:'',
        code:'',
        isAgree: false,

      },
        codeLoading:false,
        codeDisabled:false,
        loginDisabled:false,
        isSubmit:false
    }
  },
    methods: {
    submit () {
      this.$refs.form.validate().then((result) => {
        if(result){
          let _this=this
          this.isSubmit=true;

					const data = {
						phone:this.validateForm.phone,
						password:this.validateForm.password
					};

         this.axios .post(this.baseUrl+"/admin_login",data)
          .then(function(response) {
						 console.log(response);
          if(response.status==200){
              _this.$toast.message('登录成功!');
							sessionStorage.setItem('admin1111',JSON.stringify(response.data));
              sessionStorage.setItem('admin',response.data.list.admin);
              sessionStorage.setItem('keys',response.data.list.privateKeys);
              sessionStorage.setItem('img_cfg',response.data.list.img_cfg);
              sessionStorage.setItem('rank',response.data.list.rank);
              sessionStorage.setItem('_total_startTime',response.data.list._total_startTime);
              sessionStorage.setItem('_classList',response.data.list._classList);
              sessionStorage.setItem('_gradeList',response.data.list._gradeList);
              sessionStorage.setItem('power',response.data.list.power);
              $(".login").css({'transform':'rotate(5deg)','transform-origin':'0 0 '})
                setTimeout(function(){
              $(".login").css({'transform':'translate(0,100%)'})
              _this.isSubmit=false
              _this.isShow=false
                  setTimeout(function(){
                    location.replace('/')

                  },500)


              },1000)

          }else{
              alert(response.data.data)
              _this.isSubmit=false
              // _this.isShow=false
          }
        })
        .catch(function(error) {
          alert("网络错误！请稍后重试")
          _this.isSubmit=false
          // _this.isShow=false
          console.log(error);
        });
        }
      });
    },
    clear () {
      this.$refs.form.clear();
      this.validateForm = {
        username: '',
        password: '',
        phone:'',
        code:'',
        isAgree: false
      };
    },
    getCode(){
        $(".buttonDiv1").css({'width':'150px'})
        this.codeLoading=true
        this.codeDisabled=true
    }
  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
  .login{
    width:100%;
    height: 100%;
    background:url("../../../src/assets/login_bg.png") no-repeat 100%;
    position: fixed;
    top: 0;
    transition:all 1s;
    z-index:201471811111111

  }
  .loginDiv{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -190px;
    margin-top: -260px;
    box-sizing: border-box;
    width: 380px;
    height: 420px;
    padding: 30px 30px 40px;
    background: #fff;
    box-shadow: 0 20px 30px 0 rgba(63,63,65,.06);
    border-radius: 10px;

  }
  .loginTitle{}
  .tittle{
      color: #84b839;
      font-size:24px;
    font-weight: 800;
    margin-bottom: 10px;
  }
.buttonDiv1,.buttonDiv2,.buttonDiv3{
 width:100%;
 margin:auto;
 transition: all .5s
}
.buttonDiv3{

    position: relative;
}
.mu-form-item .mu-button{
    margin: 6px 0px;

}
.loginCircle{
    border-radius:50%;
    width:24px;
    height: 24px;
    position: absolute;
    top: 50%;
    left:50%;
}
.circle1{
    border-bottom:2px solid #fff;

    margin-left:-12px;
    margin-top:-12px;
    animation: mymove 1.5s linear infinite;
}
.circle2{
        border-right:2px solid #fff;

     margin-left:-14px;
    margin-top:-14px;
    width:28px;
    height:28px;

    animation: mymove 0.8s linear infinite;
}
.circle3{
        border-left:2px solid #fff;

    margin-left:-16px;
    margin-top:-16px;
    width:32px;
    height: 32px;

    animation: mymove 1.2s linear infinite;
}
@keyframes mymove
{
   from{
                /*动画开始角度*/
                transform: rotate(0deg);
            }
            to {
                /*动画结束时角度*/
                transform: rotate(360deg);
            }
}

@-webkit-keyframes mymove /*Safari and Chrome*/
{
   from{
                /*动画开始角度*/
                transform: rotate(0deg);
    }
    to {
                /*动画结束时角度*/
                transform: rotate(360deg);
    }
}
</style>



