<template>
  <div class="user">
    <div class="searchDiv" >

      <span>

        <el-select v-model="type" placeholder="请选择类型">
          <el-option v-for="(v,index) in typeList" :label="v.label" :key="v.value" :value="v.value" ></el-option>

          </el-select>
        </span>
      <span>
        <el-select v-model="state" placeholder="请选择类型">
          <el-option v-for="(v,index) in stateList" :label="v.label" :key="v.value" :value="v.value" ></el-option>

          </el-select>
        </span>

      <span class="timeSpan" v-show="clientSize">

            <el-date-picker
              v-model="timeArr"
              type="datetimerange"
              :picker-options="pickerOptions2"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              align="right">
            </el-date-picker>
        </span>
      <span class="inputSpan"><el-input placeholder="输入手机号查找" v-model="keyword"></el-input></span>

      <span class="searchButtonDiv">
             <el-button @click="screen" type="primary" plain >
                搜索
            </el-button>

            <el-button @click="centerDialogVisible=true"   plain type="success">
                添加
            </el-button>
        </span>
    </div>
    <div class="contentDiv">

      <el-table :data="tableData" border style="width: 100%;text-align: center">
        <!--<el-table-column prop="_countyCode" label="国家区号" fixed show-overflow-tooltip align="center"></el-table-column>-->
        <el-table-column prop="_phone" label="手机号" align="center"  show-overflow-tooltip></el-table-column>
        <el-table-column prop="_phone_code" label="验证码" align="center"  show-overflow-tooltip></el-table-column>
        <el-table-column prop="_create_time" label="发送时间" align="center"></el-table-column>
        <el-table-column prop="_type" label="验证码类型" align="center"></el-table-column>
        <el-table-column prop="_status" label="验证码状态" align="center" width="100"></el-table-column>
        <el-table-column prop="_use_time" label="验证码使用时间" align="center" show-overflow-tooltip></el-table-column>

        <!--<el-table-column align="center" fixed="right" label="操作">-->
          <!--<template slot-scope="scope">-->
            <!--&lt;!&ndash;<el-button type="text" size="small">审核</el-button>&ndash;&gt;-->
            <!--&lt;!&ndash;<el-button type="text" @click="stateHandle(scope.row)" :disabled="scope.row._type!='后台充值(gvl->eth)'" size="small">退回</el-button>&ndash;&gt;-->
          <!--</template>-->
        <!--</el-table-column>-->
      </el-table>
      <div class="block">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[30, 50, 80, 100]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="输入手机号即可为该手机添加一组验证码"
      :visible.sync="centerDialogVisible"
      width="640px"
      center>
      <div style="margin:auto;width:508px">
        <span class="pasInput">
            <el-input style="width:250px" placeholder="输入手机号" v-model="phone"></el-input>
      </span>
        <span class="pasInput">
            <el-input  style="width:250px" placeholder="输入验证码" v-model="code"></el-input>
      </span>
      </div>

      <div style="clear:both"></div>

      <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="primary" :loading="addLoading"  @click="updateHandle">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "user",
    components: {},
    mounted() {
      if(document.body.clientWidth<800){
        this.clientSize=false
      }
      let allScreen=document.body.clientWidth

      let windowWidthHeader=allScreen-145
      let windowWidthLeft=allScreen-64


      let coll=sessionStorage.getItem('collapse')

      if(coll){

        $(".user").css({'width':windowWidthHeader})


      }else{

        $(".user").css({'width':windowWidthLeft})

      }
      this.admin=sessionStorage.getItem('admin')
      this.keys=sessionStorage.getItem('keys')
      this.screen()
    },
    data() {
      return {
        clientSize:true,
        pickerOptions2: {
          shortcuts: [
            {
              text: "最近一周",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "最近一个月",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "最近三个月",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit("pick", [start, end]);
              }
            }
          ]
        },
        selects: [],

        admin:'',
        keys:'',
        timeArr: null,
        keyword: "",
        state: "",
        type: "",
        typeList: [{value:'',label:'全部'},{value:'注册',label:'注册'},{value:'资金密码',label:'资金密码'},{value:'修改密码',label:'修改密码'}],
        stateList: [{value:'',label:'全部'},{value:'已超时',label:'已超时'},{value:'已使用',label:'已使用'}],
        page: 1,
        pageSize: 30,
        list: [

        ],
        tableData: [],
        listTotal: 0,
        exportLoading:false,
        centerDialogVisible:false,
        phone:'',
        code:'111111',
        addLoading:false


      };
    },
    methods: {
      updateHandle(){


          this.addLoading=true
          let  _this=this
          let postData = new FormData();
          postData.append("keys", this.keys); // 用户id
          postData.append("admin", this.admin); //管理员名
          postData.append("phone", this.phone); //更改会员ID
          postData.append("code", this.code); //更改会员ID
          this.axios
            .post(this.baseUrl+"/user/addPhoneCode", postData)
            .then(function(response) {
              console.log(response)
              if(response.data.result==100){
                _this.$toast.message('操作成功！');
                _this.list_db();
                _this.centerDialogVisible=false
              }else{
                _this.$toast.message(response.data.data);
              }
              _this.addLoading=false
            })
            .catch(function(error) {
              _this.$toast.message('网路错误！');
              console.log(error);
              _this.addLoading=false

            });
      },
      handleSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        this.pageSize=val;
        this.list_db()

      },
      handleCurrentChange(val) {
        // console.log(`当前页: ${val}`);
        this.page=val;
        this.list_db()
      },

      handleSortChange({ name, order }) {
        this.list = this.list.sort(
          (a, b) => (order === "asc" ? a[name] - b[name] : b[name] - a[name])
        );
      },
      removeSelect(selectIndex) {
        const index = this.selects.indexOf(selectIndex);
        this.selects.splice(index, 1);
      },
      list_db() {
        let _this=this
        let loading = this.$loading()
        let dateStart = this.timeArr === null ? 1 : this.timeArr[0].getTime() / 1000;
        let dateEnd = this.timeArr === null ? Math.ceil(new Date().getTime() / 1000) : this.timeArr[1].getTime() / 1000;
        let postData = new FormData();
        postData.append("size", this.pageSize); //单页最大条数
        postData.append("start", dateStart); //开始时间
        postData.append("end", dateEnd); //结束时间
        postData.append("page", this.page); //当前分页
        postData.append("keyword", this.keyword); //关键词 用户id
        postData.append("admin", this.admin); //管理员名
        postData.append("keys", this.keys); //管理员秘钥
        postData.append("status", this.state); //状态
        postData.append("type", this.type); //类型

        this.axios
          .post(this.baseUrl+"User/phoneCodeSearch", postData)
          .then(function(response) {
            console.log(response)
            if(response.data.result==100){
              _this.tableData=response.data.list
              _this.listTotal=response.data.total
            }else{
              _this.list=[]
              _this.listTotal=0
            }
            loading.close()
          })
          .catch(function(error) {
            _this.list=[]
            _this.listTotal=0

            console.log(error);
            loading.close()

          });
      },
      export_db() {
        this.exportLoading=true
        let _this=this
        let dateStart = this.timeArr === null ? 1 : this.timeArr[0].getTime() / 1000;
        let dateEnd = this.timeArr === null ? Math.ceil(new Date().getTime() / 1000) : this.timeArr[1].getTime() / 1000;
        let postData = new FormData();

        postData.append("start", dateStart); //开始时间
        postData.append("end", dateEnd); //结束时间

        postData.append("keyword", this.keyword); //关键词 用户id
        postData.append("admin", this.admin); //管理员名
        postData.append("keys", this.keys); //管理员秘钥
        postData.append("type", this.type); //充值类型
        this.axios
          .post(this.baseUrl+"Coin/export",postData,{responseType: 'blob'})
          .then(function(response) {
            var blob = new Blob([response.data])
            var downloadElement = document.createElement('a');
            var href = window.URL.createObjectURL(blob); //创建下载的链接
            downloadElement.href = href;
            downloadElement.download = _this.keyword+'交易数据.csv'; //下载后文件名
            document.body.appendChild(downloadElement);
            downloadElement.click(); //点击下载
            document.body.removeChild(downloadElement); //下载完成移除元素
            window.URL.revokeObjectURL(href); //释放掉blob对象

            _this.exportLoading=false
          })
          .catch(function(error) {
            _this.exportLoading=false
            console.log(error);
          });
      },
      screen(){
        this.page=1;

        this.list_db()
      }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  .user {
  }
  .searchDiv,
  .contentDiv {
    border-radius: 1px;
    box-shadow: 1px 1px 1px #eee;

    background-color: #fff;
    padding: 10px;
    text-align: left;
    overflow: hidden;
    margin-top:10px;
  }
  .searchDiv > span {
    display: block;
    float: left;
    margin: 10px;
  }
  .searchButtonDiv {
    height: 56px;
    padding: 0px 0;
    overflow: hidden;
  }
  .tablePaper {
    width: 100%;
  }
  .totalbtn{
    margin:10px
  }
  .tablePaper {
    width: 100%;
    padding-bottom: 10px;
  }
  .block{

    text-align: center
  }

  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 46%;
    text-align: left;
  }
</style>



