<template>
	<div class="user">
		<div class="searchDiv">
			<span>
				 <el-cascader
					v-model="value"
					:options="options"
					@change="handleChange">
				</el-cascader>
				班级分数:{{pointer}}
			</span>		
		</div>

	</div>
</template>

<script>
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')

			console.log(this.admin, this.keys);
			
		},
		data() {
			return {
				clientSize: true,
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				options:[],
				value: [],
				pointer:'',
			};
		},
		created(){
			this.search();
		},
		methods: {
			handleChange(value){
				console.log(value)
				this.pointer = value[2];
			},
			search() {
			    const _this = this;
				let data = {
					adminName: this.admin,
					keys: this.keys,
				};
				
				
				this.axios.post(this.baseUrl + "/admin_cascaderUserList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.options = response.data.list;
							_this.$toast.message('设置成功！');
							
					
						} else {
							_this.$toast.message(response.data.data);
						}
						
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false
				
					});
			}	
			
		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



