<script>
	import Clipboard from 'clipboard';
  import html2canvas from 'html2canvas'
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			let allScreen = document.body.clientWidth

			let windowWidthHeader = allScreen - 145
			let windowWidthLeft = allScreen - 64


			let coll = sessionStorage.getItem('collapse')

			if (coll) {

				$(".user").css({
					'width': windowWidthHeader
				})


			} else {

				$(".user").css({
					'width': windowWidthLeft
				})

			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
      let time = sessionStorage.getItem('_total_startTime')
      time=Number(time)*1000
      this.value1=new Date(time)
      this.initdata();
			for(var i=1;i<=18;i++){
				this.weekList.push({
					value: i,
					label: i,//`第${i}周`
				})
			};
			console.log(this.weekList)
			this.initdata();
		},
		created(){

		},
		data() {
			return {
				clientSize: true,
				selects: [],
				value1:  sessionStorage.getItem('_total_startTime'),
				admin: '',
				keys: '',
				keyword: "",
				state: "",
				type: "",
				weekVal:1,
				weekList:[],
				page: 1,

				tableData: [],
				listTotal: 0,
				exportLoading: false,

			};
		},
		methods: {
			export_db(){
				html2canvas(document.getElementById('test')).then(function(canvas) {
					document.body.appendChild(canvas);
					const imageData=canvas.toDataURL()
					var aElement = document.createElement('a');
					aElement.setAttribute('href',imageData);    //重点看这里
					aElement.setAttribute('download','test.png');    //重点看这里
					document.body.insertAdjacentElement('beforeend',aElement)
					aElement.click();
				});
			},
			initTable() {
				const data = this.tableData;
				let str = '';
				console.log('1_this.tableData', this.tableData);

				str=`<div class="table-content" style='width:320px'>`;
				str+=`<div class="table-content-cell" style='display: flex;	border: 1px solid #ddd;align-items: center;'>
								<div class="left" style='width: 80px;min-width:80px;text-align: center;'>称重</div>
								<div class="right" style='display: flex;flex-direction: column;'>
									<div class="right-cell" style='display: flex;flex-direction: row;width: 400px;flex-wrap: wrap;'>
										<div class="class" style='text-align: center;	width: 80px;	height: 40px;	line-height:40px;border: 1px solid #ddd;box-sizing: border-box;'>班级</div>
										<div style='text-align: center;	width: 80px;	height: 40px;	line-height:40px;border: 1px solid #ddd;box-sizing: border-box;'>
											<div>纸质垃圾</div>
										</div>
										<div style='text-align: center;	width: 80px;	height: 40px;	line-height:40px;border: 1px solid #ddd;box-sizing: border-box;'>
											<div>塑料瓶罐</div>
										</div>
									</div>
								</div>
							</div>`;

				for(var i=0;i<data.length;i++){
					str+=`<div class="table-content-cell" style='display: flex;	border: 1px solid #ddd;align-items: center;'>
							<div class="left" style='width: 80px;min-width:80px;text-align: center;'>`+data[i]._grade+`</div>
							<div class="right" style='display: flex;flex-direction: column;'>`

						for(var j=0;j<data[i].gradeDetail.length;j++){
							str+=`<div class="right-cell" style='display: flex;flex-direction: row;width: 400px;flex-wrap: wrap;'>
									<div class="class" style='text-align: center;	width: 80px;line-height:40px;	height: 40px;	border: 1px solid #ddd;box-sizing: border-box;'>`+data[i].gradeDetail[j]._class+`</div>
									`;

									for(var k=0;k<data[i].gradeDetail[j].classDetail.length;k++){
										str+=`<div style='text-align: center;	width: 80px;line-height:40px;	height: 40px;	border: 1px solid #ddd;box-sizing: border-box;'>

											<div >`+data[i].gradeDetail[j].classDetail[k].allNumber+`</div>
											
											
										</div>`;
									};

									//str+=`</div>`;
								str+=`</div>`;
						};


						str+=`</div>`;
					str+=`</div>`
				};
				str+=`</div>`;


			$("#test").html(str);


				//生成图片 可支持下载
				/* html2canvas(document.getElementById('test')).then(function(canvas) {
					document.body.appendChild(canvas);
					const imageData=canvas.toDataURL()
					var aElement = document.createElement('a');
					aElement.setAttribute('href',imageData);    //重点看这里
					aElement.setAttribute('download','test.png');    //重点看这里
					document.body.insertAdjacentElement('beforeend',aElement)
					aElement.click();
				}); */

			},
			initdata() {
				const _this = this;
				console.log('keyword', this.keyword);
				if (this.value1 == '') {
					_this.$toast.message('选择开学时间');
					return;
				}
				let timeValue = Math.ceil((new Date(this.value1).getTime()) / 1000);
				console.log('this.value1', this.value1);
				let data = {
					adminName: this.admin,
					keys: this.keys,
					startTime:timeValue,
					week:this.weekVal,
				};


				this.axios.post(this.baseUrl + "/admin_total_type", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('查询成功！');
							const data = response.data.list;
							_this.tableData = data;
							_this.initTable();

							//_this.listTotal = response.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},


		}
	};
</script>

