<template>
	<div class="user">
		<div class="searchDiv">



			<span class="inputSpan">
				<el-input placeholder="请输入关键字" v-model="keyword"></el-input>
			</span>


			<span class="searchButtonDiv">
				<el-button type="primary" @click="search()">
					搜索
				</el-button>
			</span>
		</div>

		<div class="contentDiv">

			<el-table :data="tableData" style="width: 100%;text-align: center" max-height="800">

				<el-table-column prop="_mid" label="	信息ID" align="center">
				</el-table-column>

				<el-table-column prop="_userid" show-overflow-tooltip label="	用户id" align="center">
				</el-table-column>
				<el-table-column prop="_message" show-overflow-tooltip label="	留言信息" align="center">
				</el-table-column>
				<el-table-column prop="_state" label="		留言状态" align="center">
				</el-table-column>
				<!-- <el-table-column prop="_apply_message" label="回复信息" align="center">
				</el-table-column> -->

				<el-table-column label="回复操作" align="center" >
					<template slot-scope="scope">
						<el-button type='text' @click="dilangOpen(scope.row)" size="small">回复</el-button>
					</template>
				</el-table-column>
			</el-table>

			<div class="block">

				<el-pagination background @size-change="handleSizeChange"  @current-change="handleCurrentChange" layout="total,  prev, pager, next, jumper"
				 :total="listTotal">
				</el-pagination>
			</div>

				<el-dialog :title="'留言回复'" :visible.sync="centerDialogVisible1" width="800px" height="900px"
				 center>
				 <el-row>
					<el-col :span="24" class="warp-main">
							<el-form ref="infoForm" :model="infoForm"  label-width="120px">
								<el-form-item label="信息id" class="colorB" prop="_tittle">
									<el-input v-model="infoForm._mid" disabled></el-input>
								</el-form-item>
								<el-form-item label="回复内容" class="colorB" prop="_detail">
									<!-- <el-input v-model="infoForm._detail"></el-input> -->
									<quill-editor v-model="infoForm.message"
									 ref="myQuillEditor" :options="editorOption"
									 @blur="onEditorBlur($event)"
									 @focus="onEditorFocus($event)"
									 @ready="onEditorReady($event)"
									 @change="onEditorChange($event)" >
									</quill-editor>
								</el-form-item>
								<el-form-item>
									<el-button type="danger" plain @click="onSubmit()">确认提交</el-button>
								</el-form-item>
							</el-form>
						</el-col>


					</el-row>

				</el-dialog>

		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import hljs from 'highlight.js'
	import 'quill/dist/quill.core.css'
	import 'quill/dist/quill.snow.css'
	import 'quill/dist/quill.bubble.css'
	import {quillEditor} from 'vue-quill-editor';
	Vue.use(quillEditor);

	export default {
		name: "user",
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')

			console.log('params',this.$route.params.id);
			this.screen();
		},
		data() {
			return {
				clientSize: true,
				selects: [],
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				timeArr: null,
				keyword: "",
				type: "",
				state: "",
				class: '',
				grade: '',
				page: 1,
				pageSize: 10,
				typeVal: '_name',
				list: [],
				tableData: [],
				listTotal: 0,
				centerDialogVisible1: false,
				infoForm:{
					adminName:sessionStorage.getItem('admin'),
					keys:sessionStorage.getItem('keys'),
					_mid:"",
					message:"1111",
				},
				checkLoading: false,
				editorOption: {
					modules: {
						toolbar: [
							['bold', 'italic', 'underline', 'strike'],
							['blockquote', 'code-block'],
							[{
								'header': 1
							}, {
								'header': 2
							}],
							[{
								'list': 'ordered'
							}, {
								'list': 'bullet'
							}],
							[{
								'script': 'sub'
							}, {
								'script': 'super'
							}],
							[{
								'indent': '-1'
							}, {
								'indent': '+1'
							}],
							[{
								'direction': 'rtl'
							}],
							[{
								'size': ['small', false, 'large', 'huge']
							}],
							[{
								'header': [1, 2, 3, 4, 5, 6, false]
							}],
							[{
								'font': []
							}],
							[{
								'color': []
							}, {
								'background': []
							}],
							[{
								'align': []
							}],
							['clean'],
							['link', 'image', 'video']
						],
						syntax: {
							highlight: text => hljs.highlightAuto(text).value
						}
					}
				},
			};
		},
		mounted() {
			this.initdata();
		},
		components: {
			quillEditor
		},
		computed:{
			 editor() {
				return this.$refs.myQuillEditor.quill
			},

		},
		methods: {
			onEditorBlur(quill) {
				console.log('editor blur!', quill)
			},
			onEditorFocus(quill) {
				//this.editor.enable(false);
				console.log('editor focus!', quill);
				console.log(quill.editor.delta.ops[0].insert);
				//this.infoForm._detail = quill.editor.delta.ops[0].insert;
			},
			onEditorReady(quill) {
				console.log('editor ready!', quill)
			},
			onEditorChange({ quill, html, text }){
				console.log('html',html,text);
				this.infoForm._detail = text;
			},
			onSubmit(){
				//this.$refs.upload.submit();
				let _this = this;
				console.log('_this.infoForm',_this.infoForm);
				this.axios
					.post(this.baseUrl + "/admin_messageReplay", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('操作成功！');
							_this.centerDialogVisible1 = false;
							_this.initdata();
						} else {
							_this.$toast.message('操作失败！');
								_this.centerDialogVisible1 = true;
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});

			},
			initdata() {
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					keyword: this.keyword,
					page: this.page,
					pageSize: this.pageSize
				};
				this.axios.post(this.baseUrl + "/admin_messageList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('查询成功！');
							_this.tableData = response.data.list;
							_this.listTotal = response.data.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false
					});
			},
			search() {
				this.initdata();
			},

			dilangOpen(row) {
				console.log(row)
				this.centerDialogVisible1 = true
				this.infoForm = row;

				this.infoForm= {
					adminName:sessionStorage.getItem('admin'),
					keys:sessionStorage.getItem('keys'),
					_mid:row._mid,
					message:'',
				};
			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.page = val;
				this.initdata();
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.pageSize = val;
				this.initdata();
			},
			handleSortChange({
				name,
				order
			}) {
				this.list = this.list.sort(
					(a, b) => (order === "asc" ? a[name] - b[name] : b[name] - a[name])
				);
			},


		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

  .user {width:90%}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



