<template>
	<div id="ue">

		<div class="ueDiv">
			<el-row class="warp">

				<!--
        Form 组件提供了表单验证的功能，只需要通过 rule 属性传入约定的验证规则，并 Form-Item 的 prop 属性设置为需校验的字段名即可。具体可以参考官网：http://element.eleme.io/#/zh-CN/component/form
        -->
				<el-col :span="24" class="warp-main">
					<el-form ref="infoForm" :model="infoForm" :rules="rules" label-width="120px">

						<el-form-item label="标题" class="colorB" prop="_title">
							<el-input v-model="infoForm._title"></el-input>
						</el-form-item>

						<el-form-item label="详情" class="colorB" prop="_detail">
							<el-input v-model="infoForm._detail"></el-input>
						</el-form-item>

<!-- 						<el-form-item label="添加活动时间" class="colorB" prop="_createTime">
							<el-input v-model="infoForm._createTime"></el-input>
						</el-form-item> -->

						
						
						<el-form-item label="活动时间范围" class="colorB" prop="_createTime">
							 <el-date-picker
							  v-model="infoForm.value1"
							  type="datetimerange"
							  range-separator="至"
							  start-placeholder="开始日期"
							  end-placeholder="结束日期">
							</el-date-picker>
						</el-form-item>

						<el-form-item label="活动图片" class="colorB" prop="_createTime">
							<el-upload class="avatar-uploader"  ref="upload"  :action="uploadUrl1()" :show-file-list="false" :on-success="handleAvatarSuccess"
							  :http-request="beforeAvatarUpload"  :on-change="handleChange" :auto-upload="false" accept="image/jpeg,image/gif,image/png,image/bmp" 
							>
								<img v-if="infoForm._img" :src="infoForm._img" class="avatar">
								<i v-else class="el-icon-plus avatar-uploader-icon"></i>
							</el-upload>
						</el-form-item>
						
						

						<!-- <el-form-item label="活动开始时间" class="colorB" prop="_startTime">
							<el-input v-model="infoForm._startTime"></el-input>
						</el-form-item> -->

						<el-form-item label="活动id" class="colorB" prop="_activityId">
							<el-input v-model="infoForm._activityId"></el-input>
						</el-form-item>
						
						<!-- 
						<el-form-item label="活动结束时间" class="colorB" prop="_endTime">
							<el-input v-model="infoForm._endTime"></el-input>
						</el-form-item> -->
						
						<el-form-item>
							<el-button type="danger" plain @click="onSubmit()">确认提交</el-button>
						</el-form-item>
					</el-form>
				</el-col>


			</el-row>

		</div>


	</div>
</template>


<script>
	export default {
		data() {
			return {
				admin: '',
				keys: '',
				uploadUrl: '',
				
				infoForm: {
					 value1: [],
					_title: '',
					_detail: '',
					_createTime: '',
					_img: '',
					_startTime: '',
					_activityId: '',
					_endTime:''

				},
				

				type: '',
			}
		},
		computed: {
		
		},
		mounted() {
			//初始化
			console.log(this.value1);
			this.admin = sessionStorage.getItem("admin");
			this.keys = sessionStorage.getItem("keys");
			this.uploadUrl = sessionStorage.getItem("img_cfg");
			console.log('this.$route.params',this.$route.params);
			let base64;
			let data = this.$route.params.news;
			console.log(data)
			if (this.$route.params.type = "update") {
				var image = new Image();
				image.src = this.uploadUrl + '/img/2019-06-06/1559817236733_528448.jpg';
				image.setAttribute("crossOrigin", 'Anonymous');
				/* image.onload = () => {
					base64 = this.getBase64Image(image);
					
					console.log(base64);
					
					
					
				}; */

				this.infoForm =  {
						value1:[data._startTime,data._endTime],
						_title: data._title,
						_detail: data._detail,
						_createTime: data._createTime,
						_img: this.uploadUrl+data._img,
						_startTime: data._startTime,
						_activityId: data._activityId,
						_endTime:data._endTime
					
				};

			} else {
					this.infoForm =  {
						
						_title: data._title,
						_detail: data._detail,
						_createTime: new Date(),
						_img: this.uploadUrl+data._img,
						_startTime: this.value1[0],
						_activityId: data._activityId,
						_endTime:this.value1[1]
					
				};
			}



		},
		watch: {


		},
		methods: {
			handleChange(file){
				 var _this = this;
                if (file.raw) {                    
                    let reader = new FileReader() ; 
                    reader.onload = function (e) {
                       _this.contentHtml = e.target.result;
                    };
                    reader.readAsText(file.raw,'gb2312');
                   this.infoForm._img = file.url;
                }
                console.log(file, 'sssss');
			},
			uploadUrl1() {
				return this.baseUrl + "";
			},
			handleAvatarSuccess(res, file) {
				console.log('handleAvatarSuccess',res);
				//二次纸张再利用: 分数 = 重量/10   本书 ==>分数
				//图片示众  ： 表扬+0.1    批评 -0.1 
				//纸|瓶 ： g（1g） => 分数(1分)   
				//入箱与分类 ： 没分类 一个扣0.1分，2个扣0.2分 
			},
			beforeAvatarUpload(file) {
				console.log('file',file);
				let _this = this;
				//this.infoForm._shop_img = file.file;
				this.axios
					.post(this.baseUrl + "/admin_shopHandle", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 100) {
							_this.$toast.message('操作成功！');
						} else {
							_this.$toast.message('操作失败！');
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});
			},
			onSubmit(){
				this.$refs.upload.submit();
			},
			handlePictureCardPreview(file) {
				this.infoForm.a_img = file.url;
				this.dialogVisible = true;
			},
			isNumber(obj) {
				return !isNaN(parseFloat(obj)) && isFinite(obj);
			},
			getBase64Image(img) {
				var canvas = document.createElement("canvas");
				canvas.width = img.width;
				canvas.height = img.height;
				var ctx = canvas.getContext("2d");
				ctx.drawImage(img, 0, 0, img.width, img.height);
				var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
				var dataURL = canvas.toDataURL("image/" + ext);
				return dataURL;
			},
			submitUpload() {
				
			},
		},
		components: {

		}
	}
</script>

<style>
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}

	.ql-picker-label::before {
		position: absolute;
		top: 0px
	}

	.ql-picker-label svg {
		position: absolute;
	}

	.phone_bg {
		width: 364px;
		height: 727px;
		background: url("../assets/phone.png");
		float: left;

		position: absolute;
		bottom: 150px;
		left: 600px;

	}

	.ueDiv {
		padding: 50px;
		float: left;
		min-height: 800px;
		position: relative;
	}

	/* .warp {
		width: 484px;
		float: left;
	} */

	#content_box {
		width: 314px;
		height: 500px;
		background-color: #fff;
		margin: auto;

		border-radius: 30px;


	}

	.content_title {
		width: 314px;
		height: 50px;
		font-size: 16px;
		text-align: center;
		margin: auto;
		margin-top: 100px;
	}

	.content_time {
		width: 314px;
		height: 50px;
		font-size: 16px;
		color: #999;
		text-align: left;
		margin: auto;
		padding-left: 30px;

	}

	.ql-snow.ql-toolbar button,
	.ql-snow .ql-toolbar button {
		border: 1px solid #eee;
		border-left: none;
	}

	.ql-snow.ql-toolbar button:nth-child(1),
	.ql-snow .ql-toolbar button:nth-child(1) {
		border-left: 1px solid #eee
	}

	.ql-toolbar.ql-snow .ql-picker-label {
		border: 1px solid #eee;
	}

	#content_box img {
		max-width: 100%
	}

	#ue {
		position: fixed;
		top: 0px;
		right: 0;
		bottom: 0;
		overflow-x: auto;
		min-width: 820px;
		background-color: #fff;
		padding: 60px 0;
	}

	/*  .ql-align-center{
      text-align: center;
    }
     .ql-align-right{
      text-align: right;
    }
     .ql-align-justify{
      text-align: justify;
    }*/
	.ql-snow .ql-picker.ql-font,
	.ql-snow .ql-picker.ql-header,
	.ql-snow .ql-picker.ql-size {
		text-align: left;
	}

	.el-dialog__wrapper {
		z-index: 20018 !important;
	}

	.el-dialog__wrapper {
		z-index: 20180 !important
	}

	.v-modal {
		z-index: 2003 !important;
	}

	.el-form-item__content {
		color: #333;
	}

	.colorB .el-input__inner {
		color: #333 !important;
	}

	/* .ql-editor {
		height: 300px;
		overflow: auto;
	} */
</style>



