<template>
	
	
	
	
	
	<div class="user">
		<div class="searchDiv">
			<span class="searchButtonDiv">
				<el-button type="success"  @click="dilangOpen('add')">添加管理员</el-button>
			</span>
		</div>
		<div class="contentDiv">
			<el-table :data="tableData" style="width: 100%;text-align: center" >
				<el-table-column prop="id" label="管理员id" align="center">
				</el-table-column>
				<el-table-column prop="name" label="姓名"  align="center">
				</el-table-column>
				<el-table-column prop="password" label="密码"  align="center">
				</el-table-column>
				<!-- <el-table-column prop="privateKeys" label="	管理员秘钥"  align="center">
				</el-table-column> -->
				<el-table-column prop="rank" label="管理员等级"  align="center">
				</el-table-column>
				<el-table-column prop="power" label="管理员详细权限" align="center">
				</el-table-column>
				<el-table-column prop="phone" label="管理员登录手机号" align="center">
				</el-table-column>
				<el-table-column prop="lastLoginIp" label="	上次登录ip" align="center">
				</el-table-column>
				<el-table-column prop="ban" label="	账号状态" align="center">
					<template slot-scope="scope">
						
						<span v-if="scope.row.ban==0">正常</span>
						<span v-else>冻结</span>
					</template>
				</el-table-column>
				<el-table-column fixed="right" label="操作" align="center" >
					<template slot-scope="scope" style="text-align:center">			
						<el-button type="text" style="color:#409eff" size="small" @click="dilangOpen('update',scope.row)">修改</el-button>
					</template>
				</el-table-column>
				
			
			</el-table>
			
			<el-dialog :title="'管理员信息详情'" :visible.sync="centerDialogVisible" width="800px" height="800px" center  style="height:800px;">
				 <el-form :model="formData" ref="disalog" label-position="left" label-width="80px" class="demo-ruleForm">
					<el-form-item label="姓名">
						<el-input v-model="formData.name" ></el-input>
					</el-form-item>
					<el-form-item label="密码"  >
						<el-input v-model.number="formData.password"></el-input>
					</el-form-item>
					<el-form-item label="等级">
						
						<el-select v-model="formData.rank" placeholder="请选择类型">
							<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
						</el-select>
					</el-form-item>
					<!-- <el-form-item label="权限">
						<el-input v-model="formData.power"></el-input>
					</el-form-item> -->
					<el-form-item label="手机号">
						<el-input v-model="formData.phone" ></el-input>
					</el-form-item>
					<!-- <el-form-item label="修改类型">
						<el-input v-model="formData.updateType" disabled></el-input>
					</el-form-item> -->
					<el-form-item label="管理员id">
						<el-input v-model="formData._adminId" disabled></el-input>
					</el-form-item>
					<el-form-item label="账号状态">
						<!-- <el-input v-model="formData.ban" disabled></el-input> -->
						<el-select v-model="formData.ban" placeholder="请选择类型">
							<el-option v-for="(item,index) in banList" :label="item.label" :key="index" :value="item.value"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item>
						<el-button plain type="primary" :loading="handleLoading" @click="updateHandle()">提交</el-button>
					</el-form-item>
				</el-form> 
			</el-dialog>
			
			
		</div>
	</div>
</template>

<script>
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			let allScreen = document.body.clientWidth

			let windowWidthHeader = allScreen - 145
			let windowWidthLeft = allScreen - 64


			let coll = sessionStorage.getItem('collapse')

			if (coll) {
				$(".user").css({
					'width': windowWidthHeader
				})
			} else {
				$(".user").css({
					'width': windowWidthLeft
				})
			}
			
			this.initdata();
			
		},
		data() {
			return {
				tableData: [],
				list: [],
				android_obj: {},
				ios_obj: {},
				handleLoading: false,
				imageUrl: '',
				formData:{
					adminName: sessionStorage.getItem('admin'),
					keys: sessionStorage.getItem('keys'),
					name:'',
					password:'',
					rank:'',
					power:'',
					phone:'',
					updateType:'',
					_adminId:'',
					ban:''
				},
				typeList: [{
					value: '学生管理员',
					label: '学生管理员'
				}, {
					value: '老师管理员',
					label: '老师管理员'
				}, {
					value: '超级管理员',
					label: '超级管理员'
				}],
				banList:[{
					value: '0',
					label: '正常'
				},{
					value: '1',
					label: '冻结'
				}],
				centerDialogVisible:false,
			};
		},
		methods: {
			updateHandle(){
				let _this = this;
				this.axios.post(this.baseUrl + "/admin_adminUpdate", this.formData)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('提交成功！');
							_this.centerDialogVisible = false;
							_this.initdata();
						} else {
							_this.$toast.message(response.data.data);
						}
						
					}).catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
					});
			},
			dilangOpen(str,row){
				console.log(str,row)
				this.centerDialogVisible = true;
				
				
				if(str=='update'){
					this.formData =  {
							adminName:this.formData.adminName,
							keys:this.formData.keys,
							name:row.name,
							password:row.password,
							rank:row.rank,
							power:row.power,
							phone:row.phone,
							updateType:'update',
							_adminId:row.id,
							ban:row.ban
					};
				}else{
					this.formData =  {
						adminName:this.formData.adminName,
						keys:this.formData.keys,
						name:'',
						password:'',
						rank:'学生管理员',
						power:'all',
						phone:'',
						updateType:'add',
						_adminId:'0',
						ban:'0'
					};
				}
			},
			initdata() {
				const _this = this;
				let data = {
					adminName: _this.formData.adminName,
					keys: _this.formData.keys,	
				};
				this.axios.post(this.baseUrl + "/admin_adminList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('查询成功！');
							_this.tableData = response.data.list;
							_this.listTotal = response.data.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						
					}).catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
					});
			},
			
		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
/* 	.el-dialog{
		height:650px;
	} */
	
</style>
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 0 0 5px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 90%;
		text-align: left;
	}

	.demo-ruleForm {
		width: 90%;
		float: left;
		margin-right: 5%;
	}


	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}
	
	

</style>



