<template>
	<div class="user">
		<div class="searchDiv">
			<span>
				<el-select v-model="year" placeholder="请选择学年">
					<el-option v-for="(item,index) in yearList" :label="'第 '+item.label+' 学年'" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>

      <span>
				<el-select v-model="term" placeholder="请选择学期">
					<el-option v-for="(item,index) in termList" :label="'第 '+item.label+' 学期'" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>

			<span class="searchButtonDiv">
				<el-button @click="initdata()" type="primary" plain>
					搜索
				</el-button>

				<el-button @click="export_db"  :loading="exportLoading" plain type="success">
                导出
        </el-button>
			</span>
		</div>
		<div class="contentDiv" >
      <el-table
        :data="tableData"
        border
        :span-method="objectSpanMethod"
        show-summary
        id="test"
        :summary-method="getSummaries"
        style="width: 100%; margin-top: 20px;text-align: center">
        <el-table-column  label="类型" prop="_type" >

        </el-table-column>
        <el-table-column  label="学周" prop="_week" >

        </el-table-column>
        <el-table-column  label="预备一班" prop="_y_1" >

        </el-table-column>

        <el-table-column  label="预备二班" prop="_y_2">

        </el-table-column>

        <el-table-column  label="预备三班" prop="_y_3">

        </el-table-column>
        <el-table-column  label="预备四班" prop="_y_4">

        </el-table-column>

        <el-table-column  label="七年一班" prop="_7_1">

        </el-table-column>

        <el-table-column  label="七年二班" prop="_7_2">

        </el-table-column>

        <el-table-column  label="七年三班" prop="_7_3">

        </el-table-column>
        <el-table-column  label="七年四班" prop="_7_4" >

        </el-table-column>

        <el-table-column  label="八年一班" prop="_8_1">

        </el-table-column>

        <el-table-column  label="八年二班" prop="_8_2">

        </el-table-column>

        <el-table-column  label="八年三班" prop="_8_3">

        </el-table-column>
        <el-table-column  label="八年四班" prop="_8_4">

        </el-table-column>


        <el-table-column  label="九年一班" prop="_9_1">

        </el-table-column>

        <el-table-column  label="九年二班"  prop="_9_2">

        </el-table-column>

        <el-table-column  label="九年三班"  prop="_9_3">

        </el-table-column>
        <el-table-column  label="九年四班"  prop="_9_4">

        </el-table-column>

      </el-table>
			</div>

	</div>
</template>

<script>
	import Clipboard from 'clipboard';
  import html2canvas from 'html2canvas'
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 1000) {
				this.clientSize = false
			}
			let allScreen = document.body.clientWidth

			let windowWidthHeader = allScreen - 145
			let windowWidthLeft = allScreen - 64


			let coll = sessionStorage.getItem('collapse')

			if (coll) {

				$(".user").css({
					'width': windowWidthHeader
				})


			} else {

				$(".user").css({
					'width': windowWidthLeft
				})

			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
      let time = sessionStorage.getItem('_total_startTime')
      time=Number(time)*1000
      this.value1=new Date(time)
      this.initdata();
			this.initdata();
		},
		created(){

		},
		data() {
			return {
				clientSize: true,
				selects: [],
				value1: sessionStorage.getItem('_total_startTime'),
				admin: '',
				keys: '',
				keyword: "",
				state: "",
				type: "",
				weekVal:1,
				weekList:[],
				page: 1,

				tableData: [],
				listTotal: 0,
				exportLoading: false,
        year:2019,
        yearList:[{label:2019,value:2019},{label:2020,value:2020},{label:2021,value:2021},{label:2022,value:2022}],
        term:1,
        termList:[{label:1,value:1},{label:2,value:2}]

			};
		},
		methods: {
      objectSpanMethod({ row, column, rowIndex, columnIndex }) {
        if (columnIndex === 0) {
          if (rowIndex % 18 === 0) {
            return {
              rowspan:18,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        }
      },
      getSummaries(param) {
        const { columns, data } = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '总计';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += ' 分';
          } else {
            sums[index] = 'N/A';
          }
        });

        return sums;
      },
			export_db(){
        html2canvas(document.getElementById('test')).then(function(canvas) {
          //document.body.appendChild(canvas);
          const imageData=canvas.toDataURL()
          var aElement = document.createElement('a');
          aElement.setAttribute('href',imageData);    //重点看这里
          aElement.setAttribute('download','test.png');    //重点看这里
          document.body.insertAdjacentElement('beforeend',aElement)
          aElement.click();
        });
			},
			initTable() {

			},
			initdata() {
        let _this=this
				let data = {
					adminName: this.admin,
					keys: this.keys,
          year:this.year,term:this.term,

				};


				this.axios.post(this.baseUrl + "/admin_total_all", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('查询成功！');
							const data = response.data.list;
							_this.tableData = data;
							_this.initTable();

							//_this.listTotal = response.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},


		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	td{
		border:1px solid #ddd;
	}

	.table-content-cell {
		display: flex;
		border: 1px solid #ddd;
		align-items: center;
	}

	.left {
		width: 100px;
		text-align: center;
	}

	.right {
		display: flex;
		flex-direction: column;
	}

	.right-cell {
		display: flex;
		flex-direction: row;
		width: 400px;
		flex-wrap: wrap;
	}

	.right-cell>div {
		text-align: center;
		width: 100px;
		height: 40px;
		border: 1px solid #ddd;
		box-sizing: border-box;
	}

	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 100px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}

	.text-over {
		width: 1100px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap
	}
</style>



