Vue.prototype.$confirm = MessageBox.confirm;
import Loading from 'muse-ui-loading';
Vue.use(Loading,{
  overlayColor: 'rgba(0,0,0,.7)',        // 背景色
  size: 22,
  color: 'primary',                           // color
  className: '',
  text:'拼命加载中...'// loading class name
});


Vue.use(Vuex)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



