<template>
	<div id="ue">

		<div class="ueDiv">
			<el-row class="warp">

				<!--
        Form 组件提供了表单验证的功能，只需要通过 rule 属性传入约定的验证规则，并 Form-Item 的 prop 属性设置为需校验的字段名即可。具体可以参考官网：http://element.eleme.io/#/zh-CN/component/form
        -->
				<el-col :span="24" class="warp-main">
					<el-form ref="infoForm" :model="infoForm"  label-width="120px">

						<el-form-item label="商品名称" class="colorB" prop="_name">
							<el-input v-model="infoForm._name"></el-input>
						</el-form-item>

						<el-form-item label="商品价格" class="colorB" prop="_coin">
							<el-input v-model="infoForm._coin"></el-input>
						</el-form-item>

						<el-form-item label="	赞助公司" class="colorB" prop="_bind_company">
							<el-input v-model="infoForm._bind_company"></el-input>
						</el-form-item>

						<el-form-item label="	库存" class="colorB" prop="_balance">
							<el-input v-model="infoForm._balance"></el-input>
						</el-form-item>


<!-- :before-upload="beforeAvatarUpload" -->
						<!-- <el-upload class="avatar-uploader"  ref="upload"  :action="uploadUrl1()" :show-file-list="false" :on-success="handleAvatarSuccess"
						  :http-request="beforeAvatarUpload"  :on-change="handleChange" :auto-upload="false" accept="image/jpeg,image/gif,image/png,image/bmp"
						 :limit="1">
							<img v-if="infoForm._shop_img&&infoForm._updateType =='update'" :src="uploadUrl+infoForm._shop_img" class="avatar">
							<img v-if="infoForm._img&&infoForm._updateType =='add'" :src="infoForm._shop_img" class="avatar">
							<i v-else-if="!infoForm._shop_img" class="el-icon-plus avatar-uploader-icon"></i>




						</el-upload> -->

						<el-form-item label="商品图片" class="colorB" prop="_shopId">
							 <el-upload
							  action="http://106.15.238.244:3387/admin_uploadShopImg"
							  list-type="picture-card"
							   :on-success="success"

							  :show-file-list="false"
							  :data="imgData"
							  :limit="1" >
							  <img width="100%" :src="this.baseUrl +'/public'+infoForm._shop_img" alt="" v-if="infoForm._shop_img">
							  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
							</el-upload>
						</el-form-item>

						<!-- <el-form-item label="商品id" class="colorB" prop="_shopId">
							<el-input v-model="infoForm._shopId"></el-input>
						</el-form-item> -->

						<el-form-item>
							<el-button type="danger" plain @click="onSubmit()">确认提交</el-button>
						</el-form-item>
					</el-form>
				</el-col>


			</el-row>

		</div>


	</div>
</template>


<script>
	export default {
		data() {
			return {
				admin: '',
				keys: '',

				uploadUrl: '',
				infoForm: {
					adminName:sessionStorage.getItem('admin'),
					keys:sessionStorage.getItem('keys'),
					_name: '',
					_coin: '',
					_bind_company: '',
					_balance: '',
					_shop_img: '',
					_shopId: null,
					_state:'在售',

				},
				imgData:{
					adminName: sessionStorage.getItem('admin'),
					keys: sessionStorage.getItem('keys')
				},
				type: '',
			}
		},
		computed: {

		},
		mounted() {
			//初始化


			this.uploadUrl = sessionStorage.getItem("img_cfg");
			console.log(this.$route.params);

			let data = this.$route.params.news;
			console.log('data._state',data._state);

			if (this.$route.params.type == "update") {
				this.infoForm = {
					adminName:sessionStorage.getItem('admin'),
					keys:sessionStorage.getItem('keys'),
					_name: data._name,
					_coin: data._coin,
					_bind_company: data._bind_company,
					_balance: data._balance,
					_shop_img: data._shop_img, //this.uploadUrl+data._shop_img
					_shopId: data._shopId,
					_state:data._state
				};
			} else if(this.$route.params.type == "add"){
					this.infoForm._state ="在售";
					this.infoForm._shopId= "0";
					this.infoForm._name='';
					this.infoForm._coin= '';
					this.infoForm._bind_company= '';
					this.infoForm._balance= '';
					this.infoForm._shop_img= ''; //this.uploadUrl+data._shop_img

			}

			console.log('mounted',this.infoForm)

			//location.reload()

		},
		watch: {
		    $route:{
				handler(newV,oldV){

					this.uploadUrl = sessionStorage.getItem("img_cfg");
					console.log(this.$route.params);
					let data = this.$route.params.news;

					if (this.$route.params.type == "update") {
						this.infoForm = {
							adminName:sessionStorage.getItem('admin'),
							keys:sessionStorage.getItem('keys'),
							_name: data._name,
							_coin: data._coin,
							_bind_company: data._bind_company,
							_balance: data._balance,
							_shop_img: data._shop_img, //this.uploadUrl+data._shop_img
							_shopId: data._shopId,
							_state:data._state
						};
					} else if(this.$route.params.type == "add"){
							this.infoForm._state ="在售";
							this.infoForm._shopId= "0";
							this.infoForm._name='';
							this.infoForm._coin= '';
							this.infoForm._bind_company= '';
							this.infoForm._balance= '';
							this.infoForm._shop_img= ''; //this.uploadUrl+data._shop_img

					}
				}
			}

		},
		methods: {
			success(res){
				console.log(res);
				console.log(res.filePath[0]);
				this.infoForm._shop_img = res.filePath[0];
			},
			handleChange(file){
				 var _this = this;
                if (file.raw) {
                    let reader = new FileReader() ;
                    reader.onload = function (e) {
                       _this.contentHtml = e.target.result;
                    };
                    reader.readAsText(file.raw,'gb2312');
					this.infoForm._shop_img= file.url;
                }
                console.log(file, 'sssss');
			},


			onSubmit(){
				let _this = this;
				console.log('_this.infoForm',_this.infoForm)
				this.axios
					.post(this.baseUrl + "/admin_shopHandle", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('操作成功！');
						} else {
							_this.$toast.message('操作失败！');
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});

			},

			isNumber(obj) {
				return !isNaN(parseFloat(obj)) && isFinite(obj);
			},
			/* getBase64Image(img) {
				var canvas = document.createElement("canvas");
				canvas.width = img.width;
				canvas.height = img.height;
				var ctx = canvas.getContext("2d");
				ctx.drawImage(img, 0, 0, img.width, img.height);
				var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
				var dataURL = canvas.toDataURL("image/" + ext);
				return dataURL;
			}, */
			submitUpload() {

			},
		},
		components: {

		}
	}
</script>

<style>
	.el-upload--picture-card{
		overflow:hidden;
	}
</style>
<style>
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	/* .avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	} */

	.ql-picker-label::before {
		position: absolute;
		top: 0px
	}

	.ql-picker-label svg {
		position: absolute;
	}

	.phone_bg {
		width: 364px;
		height: 727px;
		background: url("../assets/phone.png");
		float: left;

		position: absolute;
		bottom: 150px;
		left: 600px;

	}

	.ueDiv {
		padding: 50px;
		float: left;
		min-height: 800px;
		position: relative;
	}



	#content_box {
		width: 314px;
		height: 500px;
		background-color: #fff;
		margin: auto;

		border-radius: 30px;


	}

	.content_title {
		width: 314px;
		height: 50px;
		font-size: 16px;
		text-align: center;
		margin: auto;
		margin-top: 100px;
	}

	.content_time {
		width: 314px;
		height: 50px;
		font-size: 16px;
		color: #999;
		text-align: left;
		margin: auto;
		padding-left: 30px;

	}

	.ql-snow.ql-toolbar button,
	.ql-snow .ql-toolbar button {
		border: 1px solid #eee;
		border-left: none;
	}

	.ql-snow.ql-toolbar button:nth-child(1),
	.ql-snow .ql-toolbar button:nth-child(1) {
		border-left: 1px solid #eee
	}

	.ql-toolbar.ql-snow .ql-picker-label {
		border: 1px solid #eee;
	}

	#content_box img {
		max-width: 100%
	}

	#ue {
		position: fixed;
		top: 0px;
		right: 0;
		bottom: 0;
		overflow-x: auto;
		min-width: 820px;
		background-color: #fff;
		padding: 60px 0;
	}

	/*  .ql-align-center{
      text-align: center;
    }
     .ql-align-right{
      text-align: right;
    }
     .ql-align-justify{
      text-align: justify;
    }*/
	.ql-snow .ql-picker.ql-font,
	.ql-snow .ql-picker.ql-header,
	.ql-snow .ql-picker.ql-size {
		text-align: left;
	}

	.el-dialog__wrapper {
		z-index: 20018 !important;
	}

	.el-dialog__wrapper {
		z-index: 20180 !important
	}

	.v-modal {
		z-index: 2003 !important;
	}

	.el-form-item__content {
		color: #333;
	}

	.colorB .el-input__inner {
		color: #333 !important;
	}

	.ql-editor {
		/* height: 300px; */
		overflow: auto;
	}
</style>



