<script>
	import Clipboard from 'clipboard';
  import html2canvas from 'html2canvas'
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			let allScreen = document.body.clientWidth

			let windowWidthHeader = allScreen - 145
			let windowWidthLeft = allScreen - 64


			let coll = sessionStorage.getItem('collapse')

			if (coll) {

				$(".user").css({
					'width': windowWidthHeader
				})


			} else {

				$(".user").css({
					'width': windowWidthLeft
				})

			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
      let time = sessionStorage.getItem('_total_startTime')
      time=Number(time)*1000
      this.value1=new Date(time)
			this.initdata();
		},
		created(){

		},
		data() {
			return {
				clientSize: true,
				selects: [],
				value1: '',
				admin: '',
				keys: '',
			  minCount :'',
				keyword: "",
				state: "",
				type: "",
				typeList: [{
					value: '',
					label: '全部'
				}, {
					value: 'test_coin',
					label: '测试币'
				}, {
					value: 'dytc',
					label: 'DYTC'
				}],
				stateList: [{
					value: '',
					label: '全部'
				}, {
					value: '成功',
					label: '成功'
				}, {
					value: '失败',
					label: '失败'
				}],
				page: 1,
				pageSize: 30,
				list: [

				],
				tableData: [],
				listTotal: 0,
				exportLoading: false,

			};
		},
		methods: {
			export_db(){
				html2canvas(document.getElementById('test')).then(function(canvas) {
					//document.body.appendChild(canvas);
					const imageData=canvas.toDataURL()
					var aElement = document.createElement('a');
					aElement.setAttribute('href',imageData);    //重点看这里
					aElement.setAttribute('download','test.png');    //重点看这里
					document.body.insertAdjacentElement('beforeend',aElement)
					aElement.click();
				});
			},
			initTable() {
				const data = this.tableData;

				let str = '';
				console.log('1_this.tableData', this.tableData);

				str=`<div class="table-content" style='border:1px solid #ddd;width:${(this.minCount+4)*80}px;margin:0 auto;'>`;
				for(var i=0;i<data.length;i++){
					str+=`<div class="table-content-cell" style='display: flex;	border-bottom: 1px solid #ddd;align-items: center;width:${(this.minCount+4)*80}px;'>
							<div class="left" style='width: 80px;text-align: center;'>`+data[i]._grade+`</div>
							<div class="right" style='display: flex;flex-direction: column;'>`

						for(var j=0;j<data[i].gradeDetail.length;j++){
							str+=`<div class="right-cell" style='display: flex;flex-direction: row;flex-wrap: wrap;width:${(this.minCount+3)*80}px'>
									<div class="class" style='text-align: center;	width: 80px;line-height:50px;	height: 50px;border-left: 1px solid #ddd;	border-bottom: 1px solid #ddd;'>`+data[i].gradeDetail[j]._class+`</div>
									<div class="class" style='text-align: center;	width: 80px;line-height:50px;	height: 50px;border-left: 1px solid #ddd;	border-bottom: 1px solid #ddd;'>第`+data[i].gradeDetail[j]._rank+`名</div>
									<div style='text-align: center;	width: 80px;	height: 50px;	border-left: 1px solid #ddd;	border-right: 1px solid #ddd;'>
										<div  style='border-bottom:1px solid #ddd;height:25px;'>姓名</div>
										<div  style='border-bottom:1px solid #ddd;height:25px;'>得分</div></div>`;
									for(var k=0;k<data[i].gradeDetail[j].classDetail.length;k++){

											str+=`<div style='text-align: center;	width: 80px;	height: 50px;border-left: 1px solid #ddd;	border-bottom: 1px solid #ddd;'>
												<div style='border-bottom:1px solid #ddd;height:25px;'>`+data[i].gradeDetail[j].classDetail[k]._name+`</div>
												<div style='height:25px;'>`+(data[i].gradeDetail[j].classDetail[k].allNumber== null ? 0:data[i].gradeDetail[j].classDetail[k].allNumber)+`</div>
											</div>`;

									};
									if(data[i].gradeDetail[j].classDetail.length<this.minCount){
										for(var y=1;y<=(this.minCount-data[i].gradeDetail[j].classDetail.length);y++){
											str+=`<div style='text-align: center;	width: 80px;	height: 50px;	border:0px;border-bottom:1px solid #ddd;border-left:1px solid #ddd;'>
													<div style='border-bottom:1px solid #ddd;min-height:25px;'></div>
													<div></div>
												</div>`;
										}
									}else{

									}




									//str+=`</div>`;
								str+=`</div>`;
						};


						str+=`</div>`;
					str+=`</div>`
				};
				str+=`</div>`;


			$("#test").html(str);


				//生成图片 可支持下载
				/* html2canvas(document.getElementById('test')).then(function(canvas) {
					document.body.appendChild(canvas);
					const imageData=canvas.toDataURL()
					var aElement = document.createElement('a');
					aElement.setAttribute('href',imageData);    //重点看这里
					aElement.setAttribute('download','test.png');    //重点看这里
					document.body.insertAdjacentElement('beforeend',aElement)
					aElement.click();
				}); */

			},
			initdata() {
				const _this = this;
				console.log('keyword', this.keyword);
				if (this.value1 == '') {
					_this.$toast.message('选择开学时间');
					return;
				}
				let timeValue = Math.ceil((new Date(this.value1).getTime()) / 1000);
				let data = {
					adminName: this.admin,
					keys: this.keys,
					startTime: timeValue,
				};


				this.axios.post(this.baseUrl + "/admin_total_person", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('查询成功！');
							const data = response.data.studentList;
							_this.tableData = data;
							_this.minCount = response.data.theBiggestCount;
							console.log('_this.tableData', _this.tableData);
							_this.initTable();

							//_this.listTotal = response.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},


		}
	};
</script>

