			<span>
				<el-upload
				  action="http://106.15.238.244:3387/admin_uploadShopImg"
				  list-type="picture-card"
				   :on-success="success"
					
				  :show-file-list="false"
				  :data="formData"
				  :limit="1" >
				   <img width="100%" :src="imageUrl" alt="" v-if="imageUrl">
				   <i v-else class="el-icon-plus avatar-uploader-icon"></i>
				</el-upload>
				
			</span>
				
			
			<span class="searchButtonDiv">
			
				<el-button type="danger" plain @click="uploadFile()">上传商品</el-button>
			</span>
		</div>
	</div>
</template>

<script>
	
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			let allScreen = document.body.clientWidth

			let windowWidthHeader = allScreen - 145
			let windowWidthLeft = allScreen - 64


			let coll = sessionStorage.getItem('collapse')

			if (coll) {

				$(".user").css({
					'width': windowWidthHeader
				})


			} else {

				$(".user").css({
					'width': windowWidthLeft
				})

			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.getConfig()
		},
		data() {
			return {
				list: [],
				android_obj: {},
				ios_obj: {},
				handleLoading: false,
				imageUrl: '',
				formData:{
					adminName: sessionStorage.getItem('admin'),
					keys: sessionStorage.getItem('keys')
				}
			};
		},
		methods: {
			success(res){
				console.log(res);
			},
			changeFile(data){
				console.log(data)
			},
			uploadFile(){
				const _this = this;
				//仅适用于chrome 和 firefox
				//var file = new File([blob], filename, {type: contentType, lastModified: Date.now()});
				let data = {
					adminName: this.adminName,
					keys: this.keys,
					file:this.imageUrl,
				};
				
				this.axios.post(this.baseUrl + "/admin_uploadShopImg", data)
					.then(function(response) {
						console.log('response', response);
						_this.$toast.message(response.data.data);
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false
				
					});
				
			},
			handleChanges(file){
				 var _this = this;
				if (file.raw) {                    
				    let reader = new FileReader() ; 
				    reader.onload = function (e) {
				       _this.contentHtml = e.target.result;
				    };
				    reader.readAsText(file.raw,'gb2312');
					this.imageUrl = file.url;
				}
				console.log(file, 'sssss');
			},
			getConfig() {
				let _this = this
				let loading = this.$loading()
				let postData = new FormData();
				postData.append("admin", this.admin); //管理员名
				postData.append("keys", this.keys); //管理员秘钥
				this.axios
					.post(this.baseUrl + "Config/configSearch", postData)
					.then(function(response) {
						if (response.data.result == 100) {
							for (let i in response.data.list) {
								response.data.list[i]._version = Number(response.data.list[i]._version)
								if (response.data.list[i]._type == 'android') {
									_this.android_obj = response.data.list[i]
								} else if (response.data.list[i]._type == 'ios') {
									_this.ios_obj = response.data.list[i]

								}
							}

						} else {
							_this.ios_obj = {}
							_this.android_obj = {}
						}
						loading.close()
					})
					.catch(function(error) {
						_this.ios_obj = {}
						_this.android_obj = {}

						console.log(error);
						loading.close()

					});
			},
		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 0 0 5px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}

	.demo-ruleForm {
		width: 45%;
		float: left;
		margin-right: 5%;
	}


	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	/* .avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
		border: 1px dotted #ddd;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	} */
</style>



