import Vue from 'vue'
import Router from 'vue-router'
import total from '@/components/total'
import userMes from '@/components/user'
import user_phoneCode from '@/components/user_phoneCode'
import check from '@/components/id_card_check'
import upload from '@/components/upload'
import pay_record from '@/components/pay_record'
import withdraw_record from '@/components/withdraw_record'
import coin_record from '@/components/coin_record'
import config_app from '@/components/config_app'
import config_system from '@/components/config_system'
import ue from '@/components/ue'
import ue_ad from '@/components/ue_ad'
import release_record from '@/components/release_record'
import information from '@/components/information'
import information_ad from '@/components/information_ad'
import information_up from '@/components/information_up'
import news from '@/components/news'
import news_ad from '@/components/news_ad'
import admin from '@/components/admin'
import settime from '@/components/settime'
import stunumber from '@/components/stunumber'
import cascader from '@/components/cascader'
import create from '@/components/create'
import message from '@/components/message'

Vue.use(Router)
let allPower = [{
		path: '/',
		name: 'userMes',
		component: userMes
	}, {
		path: '/check',
		name: 'check',
		component: check
	},
	{
		path: '/userMes',
		name: 'userMes',
		component: userMes
	},
	{
		path: '/pay_record',
		name: 'pay_record',
		component: pay_record
	}, //upload
	{
		path: '/upload',
		name: 'upload',
		component: upload
	},
	{
		path: '/withdraw_record',
		name: 'withdraw_record',
		component: withdraw_record
	},
	{
		path: '/coin_record',
		name: 'coin_record',
		component: coin_record
	},
	{
		path: '/config_app',
		name: 'config_app',
		component: config_app
	}, {
		path: '/config_system',
		name: 'config_system',
		component: config_system
	}, {
		path: '/user_phoneCode',
		name: 'user_phoneCode',
		component: user_phoneCode
	}, {
		path: '/ue_ad',
		name: 'ue_ad',
		component: ue_ad
	}, {
		path: '/ue',
		name: 'ue',
		component: ue
	}, {
		path: '/information',
		name: 'information',
		component: information
	}, {
		path: '/news',
		name: 'news',
		component: news
	}, {
		path: '/news_ad',
		name: 'news_ad',
		component: news_ad
	}, {
		path: '/information_ad',
		name: 'information_ad',
		component: information_ad
	}, {
		path: '/information_up',
		name: 'information_up',
		component: information_up
	}
	,{
	  path: '/release_record',
	  name: 'release_record',
	  component: release_record
	},{
	  path: '/admin',
	  name: 'admin',
	  component: admin
	},{
	  path: '/settime',
	  name: 'settime',
	  component: settime
	},{
	  path: '/stunumber',
	  name: 'stunumber',
	  component: stunumber
	}
	,{
	  path: '/cascader',
	  name: 'cascader',
	  component: cascader
	},{
	  path: '/create',
	  name: 'create',
	  component: create
	},{
	  path: '/message',
	  name: 'message',
	  component: message
	}
]
let routes = []
let power = sessionStorage.getItem('power')

if (power) {
	if (power == 'all') {
		routes = allPower
	} else {
		for (let a in allPower) {
			if (power.indexOf(allPower[a].name) > 0) {
				routes.push(allPower[a])
			}
		}
	}

}





export default new Router({
	routes: routes
})



