<template>
	<div class="user">
		<div class="searchDiv">

			<span>
				<el-select v-model="typeVal" placeholder="请选择类型">
					<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>


			<span class="searchButtonDiv">
				<el-button type="primary" @click="search()">
					搜索
				</el-button>

				<!--<el-button @click="export_db"  :loading="exportLoading" plain type="success">-->
				<!--导出-->
				<!--</el-button>-->
				<el-button type="success"  @click="dilangOpen1('add')">添加活动</el-button>
			</span>
		</div>




		<div class="contentDiv">

			<el-table :data="tableData" style="width: 100%;text-align: center" max-height="800">

				<el-table-column prop="_activityId" label="图片ID" align="center">
				</el-table-column>
				<el-table-column prop="_createTime" label="创建时间"  align="center">
				</el-table-column>
				<el-table-column prop="_startTime" label="开始时间"  align="center">
				</el-table-column>
				<el-table-column prop="_endTime" label="结束时间"  align="center">
				</el-table-column>
				<el-table-column prop="_state" label="活动状态"  align="center">
				</el-table-column>
				<el-table-column prop="_title" label="活动标题" align="center">
				</el-table-column>
				<!-- <el-table-column prop="_detail" label="活动详情"  align="center">
				</el-table-column> -->
				<el-table-column label="大图" align="center">

					<template slot-scope="scope" style="text-align:center">
						<img :src="img_cfg+'/'+scope.row._img" alt="" width="50px">
					</template>
				</el-table-column>

				<el-table-column fixed="right" label="操作" align="center" >
					<template slot-scope="scope" style="text-align:center">
						<!-- <el-button  v-if="scope.row.annexUrl" type="text" style="color:#409eff" size="small" @click="downLoad(scope.row)">附件下载</el-button> -->
						<el-button type="text" style="color:#409eff" size="small" @click="dilangOpen1('update',scope.row)">修改</el-button>
						<a  v-if="scope.row.annexUrl" :href="scope.row.annexUrl" :download="scope.row.annexUrl.split('file/')[1]" target="_blank">附件</a> 
						
					</template>
				</el-table-column>


			</el-table>

			<div class="block">
				<el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" layout="total,  prev, pager, next, jumper"
				 :total="listTotal">
				</el-pagination>
			</div>
			<el-dialog :title="'正在查看用户'+scopeRow._userid+'-'+scopeRow._name+'的信息'" :visible.sync="centerDialogVisible" width="650px"
			 center>
				<p>ID：{{scopeRow._newsId}}</p>
				<p>标题：{{scopeRow._tittle}}</p>
				<div>
					<p>大图</p>
					<img style="width:300px" :src="img_cfg+scopeRow._img" alt="">
				</div>
				{{scopeRow._state}}
				<span slot="footer" class="dialog-footer" >
					<el-button   type="danger" @click="stateHandle(scopeRow)"
					 :loading="checkLoading" plain size="small">删除</el-button>

				</span>
			</el-dialog>


			<el-dialog :title="'资讯详情'" :visible.sync="centerDialogVisible1" width="800px" height="800px" center>
				<el-row class="warp">
								<el-col :span="24" class="warp-main">
									<el-form ref="infoForm" :model="infoForm"  label-width="120px">

										<el-form-item label="标题" class="colorB" prop="_title">
											<el-input v-model="infoForm._title"></el-input>
										</el-form-item>

										<el-form-item label="详情" class="colorB" prop="_detail">
											<!-- <el-input v-model="infoForm._detail"></el-input> -->
											<quill-editor v-model="infoForm._detail"
											 ref="myQuillEditor" :options="editorOption"
											 @blur="onEditorBlur($event)"
											 @focus="onEditorFocus($event)"
											 @ready="onEditorReady($event)"
											 @change="onEditorChange($event)" >
											</quill-editor>
										</el-form-item>

				<!-- 						<el-form-item label="添加活动时间" class="colorB" prop="_createTime">
											<el-input v-model="infoForm._createTime"></el-input>
										</el-form-item> -->



										<el-form-item label="活动时间范围" class="colorB" prop="_createTime">
											 <el-date-picker
											  v-model="infoForm.value1"
											  type="datetimerange"
											  range-separator="至"
											  start-placeholder="开始日期"
											  end-placeholder="结束日期">
											</el-date-picker>
										</el-form-item>

										<!-- <el-form-item label="活动图片" class="colorB" prop="_createTime">
											<el-upload class="avatar-uploader"  ref="upload"  :action="uploadUrl1()" :show-file-list="false"
											  :http-request="beforeAvatarUpload"  :on-change="handleChange" :auto-upload="false" accept="image/jpeg,image/gif,image/png,image/bmp"
											 :limit="1">
												<img v-if="infoForm._img" :src="infoForm._img" class="avatar">
												<i v-else class="el-icon-plus avatar-uploader-icon"></i>
											</el-upload>
										</el-form-item>
										 -->

										 <el-form-item label="图片" class="colorB" prop="_detail">
										 	<el-upload
										 	  action="http://106.15.238.244:3387/admin_uploadShopImg"
										 	  list-type="picture-card"
										 	   :on-success="success"

										 	  :show-file-list="false"
										 	  :data="imgData"
										 	  >


										 	  <img width="100%" :src="baseUrl +'/public'+infoForm._img" alt="" v-if="infoForm._img">


										 	  <i v-else class="el-icon-plus avatar-uploader-icon"></i>



										 	</el-upload>
										 </el-form-item>

										  <el-form-item label="附件" class="colorB" prop="_detail">

												<el-upload
													class="upload-demo"
													ref="upload"
													action="http://106.15.238.244:3387/admin_uploadFile"
													:on-preview="handlePreview"
													:on-remove="handleRemove"
													:on-success="handleSuccess"
													:file-list="fileList"
													:auto-upload="false">
													<el-button slot="trigger" size="small" type="primary">选取附件</el-button>
													<el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传附件</el-button>
													<div slot="tip" class="el-upload__tip">只能上传doc, xls, ppt, pdf, docx, xlsx, pptx文件，且不超过3MB</div>
													</el-upload>
											</el-form-item>




										<!-- <el-form-item label="活动开始时间" class="colorB" prop="_startTime">
											<el-input v-model="infoForm._startTime"></el-input>
										</el-form-item> -->

										<el-form-item label="活动id" class="colorB" prop="_activityId">
											<el-input v-model="infoForm._activityId" :disabled="true"></el-input>
										</el-form-item>

										<!--
										<el-form-item label="活动结束时间" class="colorB" prop="_endTime">
											<el-input v-model="infoForm._endTime"></el-input>
										</el-form-item> -->

										<el-form-item>
											<el-button type="danger" plain @click="onSubmit()">确认提交</el-button>
										</el-form-item>
									</el-form>
								</el-col>


							</el-row>
			</el-dialog>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import hljs from 'highlight.js'
	import 'quill/dist/quill.core.css'
	import 'quill/dist/quill.snow.css'
	import 'quill/dist/quill.bubble.css'
	import {quillEditor} from 'vue-quill-editor';
	Vue.use(quillEditor);

	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')

			console.log(this.admin, this.keys);


		},
		data() {
			return {
				fileList:[],
				fujianName:'',
				clientSize: true,
				selects: [],
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				timeArr: null,
				keyword: "",
				type: "",
				state: "",
				class: '',
				grade: '',
				page: 1,
				pageSize: 10,
				typeVal: '活动',
				stateVal:'全部',
				typeList: [{
					value: '活动',
					label: '活动'
				}],
				str:'',
				img:"",
				imgList:[],
				tableData: [],
				listTotal: 0,
				exportLoading: false,
				flykAll: 0,
				ethAll: 0,
				ethService: 0,
				flykService: 0,
				ethOk: 0,
				flykOk: 0,
				centerDialogVisible: false,
				centerDialogVisible1:false,
				scopeRow: {},
				scopeRow1:{},
				checkLoading: false,
				imgData:{
					adminName: sessionStorage.getItem('admin'),
					keys: sessionStorage.getItem('keys')
				},
				infoForm: {
					 value1: [],
					_title: '',
					_detail: '',
					_createTime: '',
					_img: '',
					_startTime: '',
					_activityId: '',
					_endTime:'',
					_updateType:'',
					annexUrl:'', 
				},
				editorOption: {
					modules: {
						toolbar: [
							['bold', 'italic', 'underline', 'strike'],
							['blockquote', 'code-block'],
							[{
								'header': 1
							}, {
								'header': 2
							}],
							[{
								'list': 'ordered'
							}, {
								'list': 'bullet'
							}],
							[{
								'script': 'sub'
							}, {
								'script': 'super'
							}],
							[{
								'indent': '-1'
							}, {
								'indent': '+1'
							}],
							[{
								'direction': 'rtl'
							}],
							[{
								'size': ['small', false, 'large', 'huge']
							}],
							[{
								'header': [1, 2, 3, 4, 5, 6, false]
							}],
							[{
								'font': []
							}],
							[{
								'color': []
							}, {
								'background': []
							}],
							[{
								'align': []
							}],
							['clean'],
							['link', 'image', ]
						],
						syntax: {
							highlight: text => hljs.highlightAuto(text).value
						}
					}
				},

			};
		},
		mounted() {
			this.initdata();


		},
		components: {
			quillEditor
		},
		computed:{
			 editor() {
				return this.$refs.myQuillEditor.quill
			},
			/*contentCode() {
				return hljs.highlightAuto(this.content).value
			}, */
		},
		methods: {
			onEditorBlur(quill) {
				console.log('editor blur!', quill)
			},
			onEditorFocus(quill) {
				//this.editor.enable(false);
				console.log('editor focus!', quill);
				console.log(quill.editor.delta.ops[0].insert);
				//this.infoForm._detail = quill.editor.delta.ops[0].insert;
			},
			onEditorReady(quill) {
				console.log('editor ready!', quill)
			},
			onEditorChange({ quill, html, text }){
				console.log('html',html,text);
				this.infoForm._detail = html;
			},

			success(res){
				console.log(res);
				console.log(res.filePath[0]);
				this.img = res.filePath[0];
				this.infoForm._img = res.filePath[0];
			},
			initdata() {
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					page: this.page,
					pageSize: this.pageSize,
					_type: this.typeVal,
				};


				this.axios.post(this.baseUrl + "/admin_newsList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('添加成功！');
							_this.tableData = response.data.list;
							_this.listTotal = response.data.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},
			search() {
				this.initdata();
			},
			dilangOpen(str,row) {
				this.centerDialogVisible = true;
				this.scopeRow = row;
			},
			dilangOpen1(str,row){
				this.centerDialogVisible1 = true;
				this.scopeRow1 = row;

				if(str=='update'){
					this.infoForm =  {
							adminName:this.admin,
							keys:this.keys,
							value1:[this.scopeRow1._startTime,this.scopeRow1._endTime],
							_title: this.scopeRow1._title,
							_detail: this.scopeRow1._detail,
							_createTime: this.scopeRow1._createTime,
							_img: this.scopeRow1._img,
							_startTime: this.scopeRow1._startTime,
							_activityId: this.scopeRow1._activityId,
							_endTime:this.scopeRow1._endTime,
							_updateType:'update',
							annexUrl:this.scopeRow1.annexUrl ? this.scopeRow1.annexUrl.split("file/")[0] : '',
					};
					//this.fileList.push(this.scopeRow1.annexUrl);
				}else{
					this.infoForm =  {
						adminName:this.admin,
						keys:this.keys,
						_title: '',
						_detail: '',
						_createTime: Math.ceil(new Date().getTime()/1000),
						_img:'',
						_startTime: this.infoForm.value1[0],
						_activityId: 0,
						_endTime:this.infoForm.value1[1],
						_updateType:'add'
					};
				}
			},
			stateHandle(row, state) {
				this.$confirm('确定删除吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(async (res) => {
						let _this = this;
						this.checkLoading = true;
						let data = {
							adminName:this.admin,
							keys:this.keys,
							_newsId:row._newsId,
						};
						this.axios
							.post(this.baseUrl + "/admin_newsDel", data)
							.then(function(response) {
								console.log(response,response.data.result);
								if (response.data.result == 200) {
									_this.$toast.message('操作成功！');
									_this.initdata();
									_this.centerDialogVisible = false
								} else {
									_this.$toast.message(response.data.data);
								}
								_this.checkLoading = false
							})
							.catch(function(error) {
								_this.$toast.message('网路错误！');
								console.log(error);
								_this.checkLoading = false

							});
				}).catch(() => {});
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.pageSize = val;
				this.list_db()

			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.page = val;
				this.initdata();
			},
			handleSortChange({
				name,
				order
			}) {
				this.list = this.list.sort(
					(a, b) => (order === "asc" ? a[name] - b[name] : b[name] - a[name])
				);
			},
			removeSelect(selectIndex) {
				const index = this.selects.indexOf(selectIndex);
				this.selects.splice(index, 1);
			},
			handleChange(file){
				 var _this = this;
			    if (file.raw) {
			        let reader = new FileReader() ;
			        reader.onload = function (e) {
			           _this.contentHtml = e.target.result;
			        };
			        reader.readAsText(file.raw,'gb2312');
			       this.infoForm._img = file.url;
			    }
			    console.log(file, 'sssss');
			},
			uploadUrl1() {
				return this.baseUrl + "";
			},

			beforeAvatarUpload(file) {
				/* console.log('file',file);
				let _this = this;
				//this.infoForm._shop_img = file.file;
				this.axios
					.post(this.baseUrl + "/admin_activityUpdate", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('操作成功！');
						} else {
							_this.$toast.message('操作失败！');
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					}); */
			},
			onSubmit(){
				let _this = this;
				console.log('_this.infoForm',_this.infoForm);
				
				if(this.infoForm._updateType =='add'){
					console.log(' this.infoForm.value1', this.infoForm.value1)
					this.infoForm._startTime=Math.ceil(new Date(this.infoForm.value1[0]).getTime()/1000);
					this.infoForm._endTime=Math.ceil(new Date(this.infoForm.value1[1]).getTime()/1000);
				};
				this.axios
					.post(this.baseUrl + "/admin_activityUpdate", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('操作成功!');
							_this.centerDialogVisible1 = false;
							_this.initdata();
						} else {
							_this.$toast.message('操作失败！');
							_this.centerDialogVisible1 = true;
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});
			},
			handlePictureCardPreview(file) {
				this.infoForm.a_img = file.url;
				this.dialogVisible = true;
			},


			submitUpload() {
				this.$refs.upload.submit();
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
				//
			},
			handlePreview(file) {
				console.log(fileList)
				console.log(file);
			},
			handleSuccess(res,file){
				console.log("res",res,'file',file);
				this.infoForm.annexUrl = res.fields.fileName;
			},
			downLoad(row){
				console.log("row",row);
				var fileName = row.annexUrl.split("file/")[1];
				console.log('fileName',fileName)
				 if (window.navigator.msSaveBlob) {  // IE以及IE内核的浏览器
					try {
					window.navigator.msSaveBlob(row.annexUrl, fileName)  // res为接口返回数据，这里请求的时候已经处理了，如果没处理需要在此之前自行处理var data = new Blob([res.data]) 注意这里需要是数组形式的,fileName就是下载之后的文件名
					// window.navigator.msSaveOrOpenBlob(res, fileName);  //此方法类似上面的方法，区别可自行百度
					} catch (e) {
					console.log(e)
					}
				} else {
					//let url = window.URL.createObjectURL(new Blob([row.annexUrl]));
					let url = row.annexUrl;
					//console.log("url",url)
					let link = document.createElement('a')
					link.style.display = 'none'
					link.href = url;
					link.setAttribute('download', fileName)// 文件名
					document.body.appendChild(link)
					//link.click();
					//document.body.removeChild(link) // 下载完成移除元素
					//window.URL.revokeObjectURL(url) // 释放掉blob对象
				}
			}


		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	/* .avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	} */
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

  .user {width:90%}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



