<template>
	<div class="topHead">
		<keep-alive>
			<router-view class="routerContent" />

		</keep-alive>
		<el-button type="text" class="quit" @click="quitOut">退出
			<i class="el-icon-warning"></i>
		</el-button>
		<div class="navHeader" color="#fff">
			<div class="dytc">五四中学</div>
			<mu-button style="float:left;vertical-align: center"  icon slot="left">
				<mu-icon value="menu"></mu-icon>
			</mu-button>
			<div>
				<mu-breadcrumbs divider="—" class="bread">
					<mu-icon value="chevron_right" slot="divider"></mu-icon>
					<mu-breadcrumbs-item v-for="item in items" :key="item.text" :disabled="item.disabled">{{item.text}}</mu-breadcrumbs-item>
				</mu-breadcrumbs>
			</div>
		</div>

		<div class="drawerLeft">


			<el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose2" :unique-opened="isTrue2"
			 :collapse="isCollapse" background-color="#fff" text-color="#333" active-text-color="#00ab6d">
				<img src="../assets/admin_av.png" class="admin_av" alt="">
				<div class="admin">{{admin}}</div>
				<el-submenu :index="v.value" v-for="(v,index) in shiftList" :key="v.value">
					<template slot="title" @click="bread(v.nav)">
						<i :class="v.ic"></i>
						<span>{{v.value}}</span>
					</template>
					<el-menu-item style="padding-right:10px" v-for="(a,index2) in v.item" :key="a.value" @click="routerTarget(a.target,a.value)"
					 :index="a.target">
						{{a.value}}
					</el-menu-item>
				</el-submenu>

			</el-menu>
		</div>

		<transition name="el-zoom-in-top">

			<div class="nav" v-show="navShow">

				<el-button style="float:left;margin: 10px 5px;" @click="routerTarget2(tag.path,tag.title)" v-for="(tag,index) in navButtonList"
				 :key="tag.name" plain size="mini" :type="tag.type">
					{{tag.name}}
					<i @click="handleClose(index)" class="el-icon-delete"></i>
				</el-button>

			</div>
		</transition>
		<div class="buttomNav" v-show="theShift.length>0">
			<mu-bottom-nav :value.sync="shift" color="#2e3d4f" shift>
				<mu-bottom-nav-item v-for="(v,index) in theShift" :key="index" :value="v.value" :title="v.value" :to="v.target"
				 :exact="isTrue" :icon="v.ico"></mu-bottom-nav-item>
			</mu-bottom-nav>
		</div>


	</div>
</template>

<script>
	import login from '@/components/login/index'
	export default {
		name: 'topHead',
		components: {

		},
		mounted() {
			if (this.navShow) {
				$(".routerContent").css({
					'top': '77px'
				})

			} else {
				$(".routerContent").css({
					'top': '33px'
				})

			}


			let _this = this


			this.admin = sessionStorage.getItem("admin");
			this.keys = sessionStorage.getItem("adminKeys");
			let rank = sessionStorage.getItem("rank");

			console.log(this.admin)
			let shiftList = new Array;

			// this.token=sessionStorage.getItem("adminToken")

			if (this.navButtonList.length >= 1) {

				$(".routerContent").css({
					'top': '119px'
				})
			} else {
				$(".routerContent").css({
					'top': '65px'
				})

			}
			//打开权限判定
			if (rank) {
				if (rank == '老师管理员' || rank == '超级管理员') {
					_this.shiftList = _this.shiftAllList
				} else {
					const Kinds = {
						"资讯管理" :1,
						"审核照片列表":2
					};

					 _this.shiftAllList.forEach((item,index)=>{
						  let status_key = item.value;
							console.log('status_key',status_key)
							let status = Kinds[status_key];

						  if( !status ){
							  shiftList.push({
									 nav: item.nav,
									 value: item.value,
									 ic: item.ic,
									 item:item.item,
							  });
						  };


							let obj = item.item;

							obj.forEach((item,index)=>{
							  status_key = item.value;
						    status = Kinds[status_key];
								if(status && status >0){
								 	console.log(item,index);
									if(index==0){
										obj.shift();
									}else if(index==item.length-1){
											obj.pop();
									}else{
											obj.splice(index,1);
									}
								}
							});
					 });

					 console.log('shiftList',shiftList)
					_this.shiftList = shiftList;

				};
			};




		},
		data() {
			return {
				admin: '',
				keys: '',
				token: '',
				isCollapse: false,
				items: [{
					text: '首页',
					disabled: false
				}],
				open: true,
				isCollapse: false,
				docked: false,
				shiftList: [

				],
				shiftAllList: [{
						nav: 'user',
						value: '学生管理',
						'ic': 'el-icon-share',
						item: [{
								'value': '学生列表',
								'target': '/check',
								'icon': true,
								'ic': 'add_circle'
							},
							{
								'value': '审核照片列表',
								'target': '/userMes',
								'icon': true,
								'ic': 'account_box'
							},
							{
								'value': '留言列表',
								'target': '/message',
								'icon': true,
								'ic': 'message'
							},

							// {'value':'管理员上传','target':'/upload','icon':true,'ic':'message'},
							/* 	 {'value':'学生积分列表','target':'/stunumber','icon':true,'ic':'message'}, */
							/* 	 {'value':'班级分数查询','target':'/cascader','icon':true,'ic':'message'} */
						]

					},
					{
						nav: 'money',
						value: '学期统计',
						'ic': 'el-icon-menu',
						item: [
							// {'value':'信息总计','target':'/total','icon':true,'ic':'add_circle'},
							{
								'value': '纸张再利用列表',
								'target': '/pay_record',
								'icon': true,
								'ic': 'add_circle'
							},
							{
								'value': '周总计列表',
								'target': '/coin_record',
								'icon': true,
								'ic': 'info'
							},
							{
								'value': '学期总计列表',
								'target': '/release_record',
								'icon': true,
								'ic': 'info'
							},

						]

					},
					{
						nav: 'setUp',
						value: '积分商城',
						'ic': 'el-icon-location',
						item: [
							/* {'value':'商品上传','target':'/config_app','icon':true,'ic':'phone_iphone'}, */
							{
								'value': '商品列表',
								'target': '/config_system',
								'icon': true,
								'ic': 'build'
							},
							{
								'value': '订单列表',
								'target': '/ue',
								'icon': true,
								'ic': 'fiber_new'
							},
							// {'value':'公告信息','target':'/ue_ad','icon':true,'ic':'info'},
						],

					},
					{
						nav: 'information',
						value: '资讯管理',
						'ic': 'el-icon-goods',
						item: [{
								'value': '新闻资讯',
								'target': '/information',
								'icon': true,
								'ic': 'phone_iphone'
							},
							{
								'value': '活动列表',
								'target': '/news',
								'icon': true,
								'ic': 'build'
							},
							// {'value':'资讯信息','target':'/ue','icon':true,'ic':'fiber_new'},
							// {'value':'公告信息','target':'/ue_ad','icon':true,'ic':'info'},
						],

					}, {
						nav: 'system',
						value: '系统管理',
						'ic': 'el-icon-setting',
						item: [{
								'value': '管理员操作',
								'target': '/admin',
								'icon': true,
								'ic': 'phone_iphone'
							},
							{
								'value': '设置开学时间',
								'target': '/settime',
								'icon': true,
								'ic': 'phone_iphone'
							},
							{
								'value': '生成学生账号',
								'target': '/create',
								'icon': true,
								'ic': 'phone_iphone'
							},
						],

					}
				],
				navButtonList: [

				],
				theShift: [],
				panel: '',
				shift: '',
				shiftTitle: '',
				routerNow: '',
				isTrue: false,
				isTrue2: true,
				windowWidthHeader: '',
				windowWidthHeader2: '',
				windowWidthLeft: '',
				navLength: 4,
				isShow: true,
				navShow: false,
				clientSize: true,

			}
		},
		methods: {
			quitOut() {
				sessionStorage.setItem('admin', '')
				sessionStorage.setItem('keys', '')
				sessionStorage.setItem('power', '')
				sessionStorage.setItem('rank', '')
				location.reload()
			},
			handleOpen(key, keyPath) {
				this.items[0]['text'] = key;
			},
			handleClose2(key, keyPath) {},
			sizeChange() {
				let allScreen = document.body.clientWidth
				this.windowWidthHeader = allScreen - 150
				this.windowWidthLeft = allScreen - 50
			},
			routerTarget2(routerPath, title) {

				this.$router.push(routerPath);



				for (let i in this.navButtonList) {
					if (this.navButtonList[i]['name'] == title) {
						this.navButtonList[i]['type'] = 'success'

					} else {
						this.navButtonList[i]['type'] = 'default'
					}
				}



				if (this.items.length > 1) {
					this.items[1]['text'] = title;
					this.items[1]['disabled'] = false;
				} else {
					this.items.push({
						'text': title,
						'disabled': false
					})
				}
			},
			routerTarget(routerPath, title) {
				this.$router.push(routerPath);
				if (this.clientSize) {
					for (let i in this.navButtonList) {
						if (title == this.navButtonList[i]['name']) {
							this.navButtonList.splice(i, 1)

						}
					}

					if (this.navButtonList.length >= this.navLength) {
						$(".imgUd").css({
							'display': 'block'
						})
					} else {
						$(".imgUd").css({
							'display': 'none'
						})

					}
					for (let i in this.navButtonList) {
						this.navButtonList[i]['type'] = 'default'
					}
					this.navButtonList.push({
						'path': routerPath,
						'name': title,
						type: 'success'
					})




					if (this.items.length > 1) {
						this.items[1]['text'] = title;
						this.items[1]['disabled'] = false;
					} else {
						this.items.push({
							'text': title,
							'disabled': false
						})
					}
				}


			},
			bread(title) {
				if (this.items[0]) {
					this.items[0]['text'] = title;
				} else {
					this.items.push({
						'text': title,
						'disabled': false
					})
				}


			},
			breadFirst(title) {

				this.items[0]['text'] = title;



			},
			handleClose(index) {
				this.navButtonList.splice(index, 1);
			},
			addBottom(icon, index1, index2) {
				if (!this.clientSize) {
					// console.log(this.shiftList[index1].item[index2])
					this.shiftList[index1].item[index2].icon = !this.shiftList[index1].item[index2].icon
					if (!this.shiftList[index1].item[index2].icon) {
						if (this.theShift.length <= 4) {

							this.theShift = []
							for (let i in this.shiftList) {
								for (let k in this.shiftList[i].item) {
									if (!this.shiftList[i].item[k].icon) {
										this.theShift.push({
											'value': this.shiftList[i].item[k].value,
											'title': this.shiftList[i].item[k].value,
											'target': this.shiftList[i].item[k].target,
											'ico': this.shiftList[i].item[k].ic,
										})
									}
								}
							}
						} else {

							this.$toast.message('导航栏已满!!');
							this.shiftList[index1].item[index2].icon = !this.shiftList[index1].item[index2].icon

						}

					} else {
						this.theShift = []
						for (let i in this.shiftList) {
							for (let k in this.shiftList[i].item) {
								if (!this.shiftList[i].item[k].icon) {
									this.theShift.push({
										'value': this.shiftList[i].item[k].value,
										'title': this.shiftList[i].item[k].value,
										'target': this.shiftList[i].item[k].target,
										'ico': this.shiftList[i].item[k].ic,
									})
								}
							}
						}
					}

					window.localStorage.setItem('nav', JSON.stringify(this.theShift))
					window.localStorage.setItem('navList', JSON.stringify(this.shiftList))
				}










			},
			clearStorage() {
				localStorage.removeItem("navList")
				localStorage.removeItem("nav")
				location.reload()

			},

			headMenutoggle() {
				this.open = false
				// this.isCollapse=false

			},
			toggle(panel) {
				this.panel = panel === this.panel ? '' : panel;
			},
		},
		watch: {

			navButtonList() {
				if (this.navButtonList.length >= 1) {
					this.navShow = true
				} else {
					this.navShow = false

				}
			},

			theShift() {
				if (this.theShift.length >= 1) {

					$(".routerContent").css({
						'top': '33px'
					})
				}

			},
			navShow() {
				if (this.navShow) {
					$(".routerContent").css({
						'top': '77px'
					})

				} else {
					$(".routerContent").css({
						'top': '33px'
					})

				}
			},
		}


	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	a {
		color: white
	}

	.bread {

		margin-top: 17px;
	}

	.navHeader {
		padding-left: 10px;
		width: 100%;
		transition: all .3s;
		position: fixed;
		top: 0;
		left: 150px;
		height: 48px;
		background-color: #fff;
		border-bottom: 1px solid #dfdfdf;
		z-index: 111;

	}

	.navLeft {
		background-color: #fff;

	}

	/*.buttomNav{*/
	/*width: 80%;*/
	/*transition: all .3s;*/
	/*position: fixed;*/
	/*bottom: 0;*/
	/*right: 0;*/
	/*z-index:2018;*/
	/*display: none;*/
	/*}*/
	.mu-popover {
		left: 0 !important
	}

	.fullItem {
		width: 90%;
		text-align: center;
		margin: 0;
		border-top: 1px solid #eee;
	}

	.drawerLeft {}

	.el-menu {
		border: none
	}

	.imgUd {
		width: 24px;
		height: 24px;
		position: absolute;
		top: 19px;
		right: 10px;
	}

	.listHeadeer {
		/*margin-top: 60px;*/
		/*background-color: #f44336;*/
		height: 64px;
		line-height: 64px;
		border-bottom: 1px solid #ff4081;
		padding-top: 8px;

	}

	.nav {
		width: 100%;
		height: 48px;
		position: fixed;
		top: 48px;
		right: 0;
		background-color: #eee;
		line-height: 48px;
		vertical-align: middle;
		transition: all .3s;
		overflow: auto;
		z-index: 10;
		padding-left: 150px;

	}

	.leftMeadiaTitle {
		display: block;
	}

	.demo-chip-wrapper {
		text-align: center;
	}

	.demo-chip {
		margin: 16px;
		margin-top: 16px;
		float: left;

	}

	.firstNav {
		display: block;
	}

	.routerContent {

		position: absolute;
		top: 77px;
		left: 0;
		transition: .5s;
		padding: 20px;
		margin-bottom: 30px;
		margin-left: 140px;
	}

	.mu-breadcrumbs-divider {
		padding: 0
	}





	.quitBtn {
		position: absolute;
		bottom: 0px;
		right: 0px;
	}

