	/*}*/
	element.style {
		padding-left: 0px;

	}

	.mu-breadcrumbs-item>a {
		color: #ccc
	}

	.dytc {
		position: absolute;
		left: -151px;
		color: #fff;
		background-color: #00ab6d;
		font-size: 26px;
		text-align: center;
		height: 48px;
		line-height: 50px;
