<template>
	<div id="ue">

		<div class="ueDiv">
			
			<el-button type='text' >
				<router-link :to="{name:'information'}">
					<el-button type="text" style="color:#409eff" size="small">返回</el-button>
				</router-link>
			</el-button>
			
			
			<el-row class="warp">

				<!--
        Form 组件提供了表单验证的功能，只需要通过 rule 属性传入约定的验证规则，并 Form-Item 的 prop 属性设置为需校验的字段名即可。具体可以参考官网：http://element.eleme.io/#/zh-CN/component/form
        -->
				<el-col :span="24" class="warp-main">
					<el-form ref="infoForm" :model="infoForm"  label-width="120px">

						<el-form-item label="标题" class="colorB" prop="_tittle">
							<el-input v-model="infoForm._tittle"></el-input>
						</el-form-item>

						<el-form-item label="详情" class="colorB" prop="_detail">
							<el-input v-model="infoForm._detail"></el-input>
						</el-form-item>
						
						<el-form-item label="权重" class="colorB" prop="_weight">
							<el-input v-model="infoForm._weight"></el-input>
						</el-form-item>
						
							
						<el-form-item label="新闻类型" class="colorB" prop="_weight">
							<el-select v-model="infoForm.typeVal" placeholder="请选择类型">
								<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
							</el-select>
						</el-form-item>
					


						<!-- <el-form-item label="活动类型" class="colorB" prop="_createTime">
							<el-input v-model="infoForm._createTime"></el-input>
						</el-form-item>
						 -->


						<el-form-item label="活动图片" class="colorB">
							 {{infoForm._updateType}}
							<el-upload class="avatar-uploader"  ref="upload"  :action="uploadUrl1()" :show-file-list="false" :on-success="handleAvatarSuccess"
							  :http-request="beforeAvatarUpload"  :on-change="handleChange" :auto-upload="false" accept="image/jpeg,image/gif,image/png,image/bmp" 
							 :limit="1">
							<!-- {{infoForm._img&&infoForm._updateType =='update'}}
							 {{infoForm._img&&infoForm._updateType =='add'}} -->
							
								<img v-if="infoForm._img&&infoForm._updateType =='update'" :src="uploadUrl+infoForm._img" class="avatar">
								<img v-if="infoForm._img&&infoForm._updateType =='add'" :src="infoForm._img" class="avatar">
								<i v-else-if="!infoForm._img" class="el-icon-plus avatar-uploader-icon"></i>
							</el-upload>
						</el-form-item>
						
						



						<el-form-item label="新闻id" class="colorB" prop="_newsId" >
							<el-input v-model="infoForm._newsId" :disabled="true"></el-input>
						</el-form-item>
						
						

						
						<el-form-item>
							<el-button type="danger" plain @click="onSubmit()">确认提交</el-button>
						</el-form-item>
					</el-form>
				</el-col>


			</el-row>

		</div>


	</div>
</template>


<script>
	export default {
		data() {
			return {
				admin: '',
				keys: '',
				uploadUrl: '',
				 value1: [],
				 type:'',
				 typeList: [{
				 	value: '校园新闻',
				 	label: '校园新闻'
				 }, {
				 	value: '表扬栏',
				 	label: '表扬栏'
				 }],
				infoForm: {
					typeVal:'',
					_tittle: '',
					_detail: '',
					_createTime: '',
					_img: '',
					_startTime: '',
					_weight:'',
					_newsId:'',
					_updateType:''
				},
				
			}
		},
		computed: {
		
		},
		/* beforeRouteLeave (to, from , next) {
		  const answer = window.confirm('Do you really want to leave? you have unsaved changes!');
		  if (answer) {
			next()
		  } else {
			next(false)
		  }
		}, */
		created() {
			//初始化
			//console.log(this.value1);
			this.admin = sessionStorage.getItem("admin");
			this.keys = sessionStorage.getItem("keys");
			this.uploadUrl = sessionStorage.getItem("img_cfg");
		},
		/* computed:{
			$route(){
				
			}
		}, */
		watch: {
            $route:{
				handler(newV,oldV){
					console.log('$router',newV,oldV);
					console.log('this.$route.params',this.$route.params);
			
					console.log('newV.params.news',newV.params.news,newV.params.news==undefined);
					
					
					
					// if(newV.params.news == undefined){
					// 	return false;
					// }
					 
					 console.log('newV.params.type == "update"',newV.params.type == "update")
					 
					 console.log(this.$route)
					if (newV.params.type == "update") {
						let data = this.$route.params.news;
						console.log(data);
						console.log("upade");
						this.type = 'update';
						this.infoForm =  {
								adminName:this.admin,
								keys:this.keys,
								typeVal:data._type,
								_type:data._type,
								_tittle: data._tittle,
								_detail: data._detail,
								_createTime: data._createTime,
								_img: data._img,
								_weight:data._weight,
								_newsId: data._newsId,
								_updateType:this.type
						};
						
					}else if(newV.params.type == "add"){
					
						this.type = 'add';
						this.infoForm._newsId = '0';
						this.infoForm =  {
								adminName:this.admin,
								keys:this.keys,
								_type:'',
								_tittle: '',
								_detail: '',
								_createTime: new Date().getTime(),
								_img:'',
								_weight:'',
								_updateType:'add'
						};
					}				
				},
				deep: true,
			}

		},

		computed:{
			
		},
		methods: {
			
			handleChange(file){
				 var _this = this;
                if (file.raw) {                    
                    let reader = new FileReader() ; 
                    reader.onload = function (e) {
                       _this.contentHtml = e.target.result;
                    };
                    reader.readAsText(file.raw,'gb2312');
                  this.infoForm._img = file.url;
                }
                console.log(file, 'sssss');
			},
			uploadUrl1() {
				return this.baseUrl + "";
			},
			handleAvatarSuccess(res, file) {
				console.log('handleAvatarSuccess',res);
				//二次纸张再利用: 分数 = 重量/10   本书 ==>分数
				//图片示众  ： 表扬+0.1    批评 -0.1 
				//纸|瓶 ： g（1g） => 分数(1分)   
				//入箱与分类 ： 没分类 一个扣0.1分，2个扣0.2分 
			},
			beforeAvatarUpload(file) {
				console.log('file',file);
				let _this = this;
				//this.infoForm._shop_img = file.file;
				this.axios
					.post(this.baseUrl + "/admin_newsUpdate", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 100) {
							_this.$toast.message('操作成功！');
						} else {
							_this.$toast.message('操作失败！');
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});
			},
			onSubmit(){
				//this.$refs.upload.submit();
				
				
				let _this = this;
				//this.infoForm._shop_img = file.file;
				
				console.log('_this.infoForm',_this.infoForm);
				this.axios
					.post(this.baseUrl + "/admin_newsUpdate", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('操作成功！');
						} else {
							_this.$toast.message('操作失败！');
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});
				
			},
			handlePictureCardPreview(file) {
				this.infoForm.a_img = file.url;
				this.dialogVisible = true;
			},
			isNumber(obj) {
				return !isNaN(parseFloat(obj)) && isFinite(obj);
			},
			getBase64Image(img) {
				var canvas = document.createElement("canvas");
				canvas.width = img.width;
				canvas.height = img.height;
				var ctx = canvas.getContext("2d");
				ctx.drawImage(img, 0, 0, img.width, img.height);
				var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
				var dataURL = canvas.toDataURL("image/" + ext);
				return dataURL;
			},
			submitUpload() {
				
			},
		},
		components: {

		}
	}
</script>

<style>
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}

	.ql-picker-label::before {
		position: absolute;
		top: 0px
	}

	.ql-picker-label svg {
		position: absolute;
	}

	.phone_bg {
		width: 364px;
		height: 727px;
		background: url("../assets/phone.png");
		float: left;

		position: absolute;
		bottom: 150px;
		left: 600px;

	}

	.ueDiv {
		padding: 50px;
		float: left;
		min-height: 800px;
		position: relative;
	}

	/* .warp {
		width: 484px;
		float: left;
	} */

	#content_box {
		width: 314px;
		height: 500px;
		background-color: #fff;
		margin: auto;

		border-radius: 30px;


	}

	.content_title {
		width: 314px;
		height: 50px;
		font-size: 16px;
		text-align: center;
		margin: auto;
		margin-top: 100px;
	}

	.content_time {
		width: 314px;
		height: 50px;
		font-size: 16px;
		color: #999;
		text-align: left;
		margin: auto;
		padding-left: 30px;

	}

	.ql-snow.ql-toolbar button,
	.ql-snow .ql-toolbar button {
		border: 1px solid #eee;
		border-left: none;
	}

	.ql-snow.ql-toolbar button:nth-child(1),
	.ql-snow .ql-toolbar button:nth-child(1) {
		border-left: 1px solid #eee
	}

	.ql-toolbar.ql-snow .ql-picker-label {
		border: 1px solid #eee;
	}

	#content_box img {
		max-width: 100%
	}

	#ue {
		position: fixed;
		top: 0px;
		right: 0;
		bottom: 0;
		overflow-x: auto;
		min-width: 820px;
		background-color: #fff;
		padding: 60px 0;
	}

	/*  .ql-align-center{
      text-align: center;
    }
     .ql-align-right{
      text-align: right;
    }
     .ql-align-justify{
      text-align: justify;
    }*/
	.ql-snow .ql-picker.ql-font,
	.ql-snow .ql-picker.ql-header,
	.ql-snow .ql-picker.ql-size {
		text-align: left;
	}

	.el-dialog__wrapper {
		z-index: 20018 !important;
	}

	.el-dialog__wrapper {
		z-index: 20180 !important
	}

	.v-modal {
		z-index: 2003 !important;
	}

	.el-form-item__content {
		color: #333;
	}

	.colorB .el-input__inner {
		color: #333 !important;
	}
/* 
	.ql-editor {
		height: 300px;
		overflow: auto;
	} */
</style>



