<template>
	<div class="user">
		<div class="searchDiv">

			<span>

				  <el-date-picker
					  v-model="value1"
					  type="date"
					  placeholder="选择日期">
					</el-date-picker>
			</span>




			<span class="searchButtonDiv">

				<el-button type="primary" @click="search()">
					设置
				</el-button>
			</span>
		</div>

	</div>
</template>

<script>
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')
			let time = sessionStorage.getItem('_total_startTime')
      // console.log(time)
      time=Number(time)*1000
      this.value1=new Date(time)

		},
		data() {
			return {
				clientSize: true,
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				//power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				value1:'',
			};
		},

		methods: {
			search() {
			    const _this = this;


				this.value1 = Math.ceil((new Date(this.value1).getTime()) / 1000);


				console.log('this.value1', this.value1);
				let data = {
					adminName: this.admin,
					keys: this.keys,
					newTime: this.value1,
				};


				this.axios.post(this.baseUrl + "/admin_totalTimeUpdate", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('设置成功！');

							 sessionStorage.setItem('_total_startTime',_this.value1);


							//_this.listTotal = response.count;

							_this.value1 = '';
						} else {
							_this.$toast.message(response.data.data);
						}

					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			}

		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



