<template>
	<div class="user">
		<div class="searchDiv">

			<span>

				<el-select v-model="typeVal" placeholder="请选择类型">
					<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>


			<span class="inputSpan">
				<el-input placeholder="请输入关键字" v-model="keyword"></el-input>
			</span>

			<span>
				<el-select v-model="stateVal" placeholder="请选择图片上传状态">
					<el-option v-for="(item,index) in stateList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>

			<span class="searchButtonDiv">

				<el-button type="primary" @click="search()">
					搜索
				</el-button>

				<el-button @click="centerDialogVisible_adminUp=true"   plain type="success">
				管理员上传
				</el-button>

			</span>
		</div>

		<div class="contentDiv">

			<el-table :data="tableData" style="width: 100%;text-align: center" max-height="800">

				<el-table-column prop="_imgId" label="图片ID" align="center">
				</el-table-column>
        <el-table-column prop="_type" label="垃圾分类" align="center">
        </el-table-column>
				<el-table-column prop="_userid" label="上传用户ID" width="78" align="center">
				</el-table-column>
        <el-table-column prop="_name" label="上传用户姓名" align="center">
        </el-table-column>
        <el-table-column prop="_grade" label="上传用户年级" align="center">
        </el-table-column>
        <el-table-column prop="_class" label="上传用户班级" align="center">
        </el-table-column>
        <el-table-column prop="_state" label="图片审核状态" align="center">
          <template slot-scope="scope">
            <p v-if="scope.row._state=='审核通过'" style="color: #42b983;">{{scope.row._state}}</p>
            <p v-else-if="scope.row._state=='审核不通过'" style="color: red;">{{scope.row._state}}</p>
            <p v-else="scope.row._state=='审核中'">{{scope.row._state}}</p>
		
          </template>
        </el-table-column>
        <el-table-column prop="_bind_activity" width="80" label="图片参加活动id" align="center">
        </el-table-column>
				<!--<el-table-column prop="_img_url" label="图片地址" align="center">-->
				<!--</el-table-column>-->

				<el-table-column prop="_bind_place" label="拍摄地点" align="center">
				</el-table-column>
				<el-table-column prop="_comment" label="点赞数" align="center">
				</el-table-column>
				<el-table-column prop="_weight" label="重量" align="center">
				</el-table-column>
				<el-table-column prop="_number" label="数量" align="center">
				</el-table-column>
				<el-table-column prop="_value" label="分数" align="center">
				</el-table-column>
				<el-table-column prop="_handleAdmin" label="处理人" align="center">
				</el-table-column>

				<el-table-column label="审核" align="center" fixed="right">
					<template slot-scope="scope">
						<!--<el-button type='text' size="small">审核</el-button>-->
						<el-button type='text' @click="dilangOpen(scope.row)" size="small" v-if="scope.row._state=='审核中'">审核</el-button>
						<el-button type='text' @click="dilangOpen(scope.row)" size="small" v-else="">查看</el-button>
						
						<!-- <el-button type='text' v-if="scope.row._state =='审核中' " @click="stateHandle(scope.row,'删除')" size="small">删除</el-button> -->
						<el-button type='text'  @click="deleleImg(scope.row,'删除')" size="small">删除</el-button>
					</template>
				</el-table-column>



			</el-table>

			<div class="block">

				<el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" layout="total,  prev, pager, next, jumper"
				 :total="listTotal">
				</el-pagination>
			</div>
			<el-dialog :title="'正在查看用户'+scopeRow._userid+'-'+scopeRow._name+'的图片信息'" :visible.sync="centerDialogVisible" width="650px"
			 center>


        <el-form  label-width="150px" >
          <el-form-item label="人员姓名:">
            <p>{{scopeRow._name}}</p>
          </el-form-item>
          <el-form-item label="获得分数:">
            <span style="color: #42b983;">{{scopeRow._value}}</span>
          </el-form-item>
          <el-form-item label="查看照片:">
            <img style="width:300px" :src="img_cfg+scopeRow._img_url" alt="">
          </el-form-item>
          <el-form-item label="当前状态:">
            {{scopeRow._state}}
          </el-form-item>
        </el-form>
				<span slot="footer" class="dialog-footer" >
					<el-button  v-if='scopeRow._state !=="审核通过"' type="danger" @click="stateHandle(scopeRow,'审核不通过')"
					 :loading="checkLoading" plain  icon="el-icon-close"></el-button>

					<el-button v-if='scopeRow._state !=="审核通过"' type="primary" @click="stateHandle(scopeRow,'审核通过')"
					 :loading="checkLoading" plain  icon="el-icon-check"></el-button>
				</span>
			</el-dialog>
      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible_adminUp"
        width="500px"
        center>
        <adUpload></adUpload>

      </el-dialog>

		</div>
	</div>
</template>

<script>
  import adUpload from './upload.vue'

  export default {
		name: "user",
		components: {
      adUpload
    },
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')

			console.log(this.admin, this.keys);

		},
		data() {
			return {
				clientSize: true,
				selects: [],
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				timeArr: null,
				keyword: "",
				type: "",
				state: "",
				class: '',
				grade: '',
				page: 1,
				pageSize: 10,
				typeVal: '_name',
				stateVal:'全部',
				typeList: [{
					value: '_name',
					label: '姓名'
				}, {
					value: '_grade',
					label: '年级'
				}, {
					value: '_class',
					label: '班级'
				}],
				stateList: [{
					value: '全部',
					label: '全部'
				}, {
					value: '审核中',
					label: '审核中'
				}, {
					value: '审核通过',
					label: '审核通过'
				}],
				imgList:[],
				tableData: [],
				listTotal: 0,
				exportLoading: false,
				flykAll: 0,
				ethAll: 0,
				ethService: 0,
				flykService: 0,
				ethOk: 0,
				flykOk: 0,
				centerDialogVisible: false,
        centerDialogVisible_adminUp: false,
				scopeRow: {},
				checkLoading: false
			};
		},
		mounted() {
			this.initdata();
		},
		methods: {
			initdata() {
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					page: this.page,
					pageSize: this.pageSize,
					_state:this.stateVal,
					type: this.typeVal,
					keyword: this.keyword,
				};


				this.axios.post(this.baseUrl + "/admin_checkList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('查询成功！');
							_this.tableData = response.data.list;
							_this.listTotal = response.data.count;
						} else {
							_this.$toast.message(response.data.data);
						}

						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},
			search() {
				this.initdata();
			},
			dilangOpen(row) {
				this.centerDialogVisible = true;
				this.scopeRow = row;
			},
			deleFN(row){
				console.log("rpow",row);
				const _this = this;
				this.$confirm('是否删除该图片？', '确认信息', {
					distinguishCancelAndClose: true,
					confirmButtonText: '删除',
					cancelButtonText: '放弃操作'
					})
					.then(() => {
						
						_this.$toast.message('操作成功');
					})
					.catch(action => {
						_this.$toast.message('取消操作');
					});
			},
			deleleImg(row, state){
				this.$confirm('确定 ' + state + ' 吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(async (res) => {
						let _this = this;
						this.checkLoading = true;
						let data = {
							adminName:this.admin,
							keys:this.keys,
							_imgList:JSON.stringify([String(row._imgId)]),
							_state:state
						};
						this.axios
							.post(this.baseUrl + "/admin_delete", data)
							.then(function(response) {
								console.log(response);
								if (response.data.result == 200) {
									_this.$toast.message('操作成功！');
								_this.initdata();
									_this.centerDialogVisible = false
								} else {
									_this.$toast.message(response.data.data);
								}
								_this.checkLoading = false
							})
							.catch(function(error) {
								_this.$toast.message('网路错误！');
								console.log(error);
								_this.checkLoading = false

							});
				}).catch(() => {});
			},
			stateHandle(row, state) {
				this.$confirm('确定 ' + state + ' 吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(async (res) => {
						let _this = this;
						this.checkLoading = true;
						let data = {
							adminName:this.admin,
							keys:this.keys,
							_imgList:JSON.stringify([String(row._imgId)]),
							_state:state
						};
						this.axios
							.post(this.baseUrl + "/admin_checkHandle", data)
							.then(function(response) {
								console.log(response);
								if (response.data.result == 200) {
									_this.$toast.message('操作成功！');
								_this.initdata();
									_this.centerDialogVisible = false
								} else {
									_this.$toast.message(response.data.data);
								}
								_this.checkLoading = false
							})
							.catch(function(error) {
								_this.$toast.message('网路错误！');
								console.log(error);
								_this.checkLoading = false

							});
				}).catch(() => {});
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.pageSize = val;
				this.list_db()

			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.page = val;
				this.initdata();
			},

			handleSortChange({
				name,
				order
			}) {
				this.list = this.list.sort(
					(a, b) => (order === "asc" ? a[name] - b[name] : b[name] - a[name])
				);
			},
			removeSelect(selectIndex) {
				const index = this.selects.indexOf(selectIndex);
				this.selects.splice(index, 1);
			},


		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



