<template>
	<div class="user">
		<div class="searchDiv">

			<span>
				<el-select v-model="typeVal" placeholder="请选择类型">
					<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>


			<span class="searchButtonDiv">
				<el-button type="primary" @click="search()">
					搜索
				</el-button>

				<!--<el-button @click="export_db"  :loading="exportLoading" plain type="success">-->
				<!--导出-->
				<!--</el-button>-->
				<el-button type="success" @click="dilangOpen1('add')">添加资讯</el-button>
			</span>
		</div>


		<div class="contentDiv">

			<el-table :data="tableData" style="width: 100%;text-align: center" max-height="800">

				<el-table-column prop="_newsId" label="图片ID" align="center">
				</el-table-column>
				<el-table-column prop="_tittle" label="标题" align="center">
				</el-table-column>
				<!-- <el-table-column prop="_detail" label="详情"  align="center">
				</el-table-column> -->
				<el-table-column prop="_weight" label="排序" align="center">
				</el-table-column>
				<el-table-column prop="_type" label="类型" align="center">
				</el-table-column>
				<el-table-column prop="_img" label="大图" align="center">
					<template slot-scope="scope">

						<img :src="img_cfg+'/'+scope.row._img" alt="" width="50px">
					</template>
				</el-table-column>

				<el-table-column label="审核" align="center">
					<template slot-scope="scope">
						<el-button type='text' @click="stateHandle(scope.row,'删除')" size="small">删除</el-button>
						<el-button type='text'>
							<el-button type="text" style="color:#409eff" size="small" @click="dilangOpen1('update',scope.row)">修改</el-button>

						</el-button>
					</template>
				</el-table-column>



			</el-table>

			<div class="block">
				<el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" layout="total,  prev, pager, next, jumper"
				 :total="listTotal">
				</el-pagination>
			</div>

			<!--<el-dialog :title="'新闻详情'" :visible.sync="centerDialogVisible" width="650px" height="650px"-->
			<!--center>-->
			<!--<p>ID：{{scopeRow._newsId}}</p>-->
			<!--<p>标题：{{scopeRow._tittle}}</p>-->
			<!--<div>-->
			<!--<p>大图</p>-->
			<!--<img style="width:300px" :src="img_cfg+scopeRow._img" alt="">-->
			<!--</div>-->
			<!--<p>新闻内容：{{scopeRow._detail}}</p>-->
			<!--{{scopeRow._state}}-->
			<!--<span slot="footer" class="dialog-footer" >-->
			<!--<el-button   type="danger" @click="stateHandle(scopeRow,'删除')"-->
			<!--:loading="checkLoading" plain size="small">删除</el-button>-->
			<!---->
			<!--</span>-->
			<!--</el-dialog>-->



			<el-upload style="display:none" class="avatar-uploader1" action="http://106.15.238.244:3387/admin_uploadShopImg"
			 list-type="picture-card" :on-success="success1" :on-error="error1" :show-file-list="false" :data="imgData" >
				<img width="100%" :src="baseUrl +'/public'+infoForm._img" alt="" v-if="infoForm._img">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>



			<el-dialog :title="'新闻详情'" :visible.sync="centerDialogVisible1" width="800px" height="900px" center>
				<el-row>
					<el-col :span="24" class="warp-main">
						<el-form ref="infoForm" :model="infoForm" label-width="120px">

							<el-form-item label="标题" class="colorB" prop="_tittle">
								<el-input v-model="infoForm._tittle"></el-input>
							</el-form-item>

							<el-form-item label="详情" class="colorB" prop="_detail">
								<!-- <el-input v-model="infoForm._detail"></el-input> -->
								<quill-editor v-model="infoForm._detail" ref="myQuillEditor" :options="editorOption" @blur="onEditorBlur($event)"
								 @focus="onEditorFocus($event)" @ready="onEditorReady($event)" @change="onEditorChange($event)">
								</quill-editor>
							</el-form-item>

							<el-form-item label="权重" class="colorB" prop="_weight">
								<el-input v-model="infoForm._weight"></el-input>
							</el-form-item>


							<el-form-item label="新闻类型" class="colorB" prop="_weight">
								<el-select v-model="infoForm.typeVal" placeholder="请选择类型" @change="changeElFn">
									<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
								</el-select>
							</el-form-item>



							<!-- <el-form-item label="活动类型" class="colorB" prop="_createTime">
								<el-input v-model="infoForm._createTime"></el-input>
							</el-form-item>
							 -->


							<!-- <el-form-item label="活动图片" class="colorB">

								<el-upload class="avatar-uploader"  ref="upload"  :action="uploadUrl1()" :show-file-list="false"
								  :http-request="beforeAvatarUpload"  :on-change="handleChange" :auto-upload="false" accept="image/jpeg,image/gif,image/png,image/bmp"
								 :limit="1">
								 {{infoForm._img&&infoForm._updateType =='update'}}
								 {{infoForm._img&&infoForm._updateType =='add'}}

									<img v-if="infoForm._img" :src="infoForm._img" class="avatar">

									<i v-else class="el-icon-plus avatar-uploader-icon"></i>
								</el-upload>
							</el-form-item> -->



							<el-form-item label="活动图片" class="colorB" prop="_img">
								<el-upload class="avatar-uploader" action="http://106.15.238.244:3387/admin_uploadShopImg" list-type="picture-card"
								 :on-success="success" :on-error="error" :show-file-list="false" :data="imgData" >
									<img width="100%" :src="baseUrl +'/public'+infoForm._img" alt="" v-if="infoForm._img">
									<i v-else class="el-icon-plus avatar-uploader-icon"></i>
								</el-upload>
							</el-form-item>






							<el-form-item label="新闻id" class="colorB" prop="_newsId">
								<el-input v-model="infoForm._newsId" :disabled="true"></el-input>
							</el-form-item>



							<el-form-item>
								<el-button type="danger" plain @click="onSubmit()">确认提交</el-button>
							</el-form-item>

							<div style="overflow:hidden;clear:both;"></div>
						</el-form>
					</el-col>


				</el-row>

			</el-dialog>



		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import hljs from 'highlight.js'
	import 'quill/dist/quill.core.css'
	import 'quill/dist/quill.snow.css'
	import 'quill/dist/quill.bubble.css'
	import {
		quillEditor
	} from 'vue-quill-editor';
	/* import {
		quillRedefine
	} from 'vue-quill-editor-upload'; */
	Vue.use(quillEditor);

	const toolbarOptions = [
		['bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block', 'image', 'video', {
			'color': []
		}], // toggled buttons


		[{
			'header': 1
		}, {
			'header': 2
		}, {
			'list': 'ordered'
		}, {
			'list': 'bullet'
		}, {
			'script': 'sub'
		}, {
			'script': 'super'
		}, {
			'direction': 'rtl'
		}, 'clean', {
			'background': []
		}], // custom button values

		['link', {
			'size': ['small', false, 'large', 'huge']
		}, {
			'header': [1, 2, 3, 4, 5, 6, false]
		}, ], // custom dropdown

	];

	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')

			console.log(this.admin, this.keys);

		},
		data() {
			return {
				clientSize: true,
				selects: [],
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				timeArr: null,
				keyword: "",
				type: "",
				state: "",
				class: '',
				grade: '',
				page: 1,
				pageSize: 10,
				typeVal: '校园新闻',
				stateVal: '全部',
				typeList: [{
					value: '校园新闻',
					label: '校园新闻'
				}, {
					value: '表扬栏',
					label: '表扬栏'
				}, {
					value: '低碳环保组',
					label: '低碳环保组'
				}],
				img: '',
				imgList: [],
				tableData: [],
				listTotal: 0,
				exportLoading: false,
				flykAll: 0,
				ethAll: 0,
				ethService: 0,
				flykService: 0,
				ethOk: 0,
				flykOk: 0,
				centerDialogVisible: false,
				centerDialogVisible1: false,
				scopeRow: {},
				scopeRow1: {},
				checkLoading: false,
				uploadUrl: '',
				value1: [],
				infoForm: {
					typeVal: '',
					_type: this.typeVal,
					_tittle: '',
					_detail: '',
					_createTime: '',
					_img: '',
					_startTime: '',
					_weight: '',
					_newsId: '',
					_updateType: '',
				},
				imgData: {
					adminName: sessionStorage.getItem('admin'),
					keys: sessionStorage.getItem('keys')
				},
				editorOption: {
					modules: {
						toolbar: {
							container: toolbarOptions,
							handlers: {
								'image': function(value) {
									if (value) {
										document.querySelector('.avatar-uploader1 input').click();
									} else {
										this.quill.format('image', false);
									}
								}
							},
						},

						syntax: {
							highlight: text => hljs.highlightAuto(text).value
						}
					}
				},

			};
		},
		components: {
			quillEditor,
			/* quillRedefine */
		},
		mounted() {
			this.initdata();
		},
		/* beforeRouteLeave (to, from , next) {
		  console.log(to,next,from)
		}, */

		computed: {
			editor() {
				return this.$refs.myQuillEditor.quill
			},

		},
		created() {
			/* this.editorOption  = quillRedefine({
				uploadConfig: {
					action: 'http://106.15.238.244:3387/admin_uploadShopImg',  // 必填参数 图片上传地址
					// 必选参数  res是一个函数，函数接收的response为上传成功时服务器返回的数据
					// 你必须把返回的数据中所包含的图片地址 return 回去
					methods: 'POST',
					accept: 'image/png, image/gif, image/jpeg, image/bmp, image/x-icon',
					header: (xhr, formData) => {
						// xhr.setRequestHeader('myHeader','myValue');
						// formData.append('token', '1234')
						formData.append('adminName',sessionStorage.getItem('admin'));
						formData.append('keys',sessionStorage.getItem('keys'));
					},
					change:(xhr, formData)=>{
						console.log(xhr, formData);
						 formData.append('adminName',sessionStorage.getItem('admin'));
						formData.append('keys',sessionStorage.getItem('keys'));
						/* xhr.setRequestHeader('adminName',sessionStorage.getItem('admin'));
						xhr.setRequestHeader('keys',sessionStorage.getItem('keys'));
					},
					success: (res) => {console.log(res)},
					error: (err) => {console.log(err) } ,
					res: (respnse) => {

						console.log('respnse',respnse)
					   return respnse.info
					},
					name: 'img'  // 图片上传参数名
				}
			}) */
		},
		methods: {
			error1() {
				this.$message.error('图片插入失败');
			},
			changeElFn(val) {
				console.log('val', val);
				this.infoForm._type = val;
			},
			onEditorBlur(quill) {
				console.log('editor blur!', quill)
			},
			onEditorFocus(quill) {
				//this.editor.enable(false);
				console.log('editor focus!', quill);
				console.log(quill.editor.delta.ops[0].insert);
				//this.infoForm._detail = quill.editor.delta.ops[0].insert;
			},
			onEditorReady(quill) {
				console.log('editor ready!', quill)
			},
			onEditorChange({
				quill,
				html,
				text
			}) {
				console.log('html', html, text);
				this.infoForm._detail = html;
			},
			success(res) {
				console.log(res);
				console.log(res.filePath[0]);
				this.img = res.filePath[0];
				this.infoForm._img = res.filePath[0];
				console.log(this.baseUrl + '/public' + this.img)
			},
			success1(res) {
				console.log('res1', res);
				let quill = this.$refs.myQuillEditor.quill
				// 如果上传成功

				if (res) {
					// 获取光标所在位置
					let length = quill.getSelection().index;
					if (res.filePath[0].indexOf('.jpg') > -1 || res.filePath[0].indexOf('.png') > -1 || res.filePath[0].indexOf(
							'.jpeg') > -1) {
						quill.insertEmbed(length, 'image', this.baseUrl + '/public' + res.filePath[0])

					} else {
						quill.insertEmbed(length, 'video', this.baseUrl + '/public' + res.filePath[0])

					}

					// 调整光标到最后
					quill.setSelection(length + 1)
				} else {
					this.$message.error('图片插入失败')
				}

			},
			error(res) {
				this.$message.error(res);
			},
			initdata() {
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					page: this.page,
					pageSize: this.pageSize,
					_type: this.typeVal,
				};
				//this.$qs.stringify()
				this.axios.post(this.baseUrl + "/admin_newsList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200 || response.data.result == 203) {
							_this.$toast.message('查询成功！');
							_this.tableData = response.data.list;
							_this.listTotal = response.data.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},
			search() {
				this.initdata();
			},
			dilangOpen1(str, row) {
				this.centerDialogVisible1 = true;
				this.scopeRow1 = row;
				console.log(row)

				if (str == 'update') {

					this.infoForm = {
						adminName: this.admin,
						keys: this.keys,
						typeVal: this.scopeRow1._type,
						_type: this.typeVal,
						_tittle: this.scopeRow1._tittle,
						_detail: this.scopeRow1._detail,
						_createTime: Math.ceil(new Date().getTime() / 1000), //this.scopeRow1._createTime
						_img: this.scopeRow1._img,
						_weight: this.scopeRow1._weight,
						_newsId: this.scopeRow1._newsId,
						_updateType: 'update'
					};
					console.log('this.infoForm.typeVal', this.typeVal);
					console.log('this.infoForm._type', this.infoForm._type);

				} else {
					this.infoForm = {
						adminName: this.admin,
						keys: this.keys,
						_type: this.typeVal,
						_tittle: '',
						_detail: '',
						_createTime: Math.ceil(new Date().getTime() / 1000),
						_img: '',
						_weight: '',
						_updateType: 'add',
						_newsId: 0
					};
				}
				//console.log('this.scopeRow1',this.scopeRow1)

			},
			dilangOpen(row) {
				this.centerDialogVisible = true;
				this.scopeRow = row;
			},
			stateHandle(row, state) {
				this.$confirm('确定删除吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(async (res) => {
					let _this = this;
					this.checkLoading = true;
					let data = {
						adminName: this.admin,
						keys: this.keys,
						_newsId: row._newsId,
					};
					this.axios
						.post(this.baseUrl + "/admin_newsDel", data)
						.then(function(response) {
							console.log(response);
							if (response.data.result == 200) {
								_this.$toast.message('操作成功！');
								_this.initdata();
								_this.centerDialogVisible = false
							} else {
								_this.$toast.message(response.data.data);
							}
							_this.checkLoading = false
						})
						.catch(function(error) {
							_this.$toast.message('网路错误！');
							console.log(error);
							_this.checkLoading = false

						});
				}).catch(() => {});
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.pageSize = val;


			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.page = val;
				this.initdata();
			},

			handleSortChange({
				name,
				order
			}) {
				this.list = this.list.sort(
					(a, b) => (order === "asc" ? a[name] - b[name] : b[name] - a[name])
				);
			},
			removeSelect(selectIndex) {
				const index = this.selects.indexOf(selectIndex);
				this.selects.splice(index, 1);
			},
			handleChange(file) {
				var _this = this;
				if (file.raw) {
					let reader = new FileReader();
					reader.onload = function(e) {
						_this.contentHtml = e.target.result;
					};
					reader.readAsText(file.raw, 'gb2312');
					this.infoForm._img = file.url;
				}
				console.log(file, 'sssss');
			},
			uploadUrl1() {
				return this.baseUrl + "";
			},
			beforeAvatarUpload(file) {
				console.log('file', file);
				let _this = this;
				//this.infoForm._shop_img = file.file;
				this.axios
					.post(this.baseUrl + "/admin_newsUpdate", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 100) {
							_this.$toast.message('操作成功！');

						} else {
							_this.$toast.message('操作失败！');

						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});
			},
			onSubmit() {
				//this.$refs.upload.submit();
				let _this = this;

				this.axios
					.post(this.baseUrl + "/admin_newsUpdate", _this.infoForm)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('操作成功！');
							_this.centerDialogVisible1 = false;
							_this.initdata();
						} else {
							_this.$toast.message('操作失败！');
							_this.centerDialogVisible1 = true;
						}
					})
					.catch(function(error) {
						_this.$toast.message('网络错误！');
						console.log(error);
					});

			},
			handlePictureCardPreview(file) {
				this.infoForm.a_img = file.url;
				this.dialogVisible = true;
			},
			isNumber(obj) {
				return !isNaN(parseFloat(obj)) && isFinite(obj);
			},



		}
	};
</script>

