		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	/* .avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	} */

	.ql-picker-label::before {
		position: absolute;
		top: 0px
	}

	.ql-picker-label svg {
		position: absolute;
	}

	.phone_bg {
