<template>
	<div class="user">
		<div class="searchDiv">

			<span>
				<el-select v-model="typeVal" placeholder="请选择类型">
					<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>
			
			<span>
				<el-select v-model="keywordTypeVal" placeholder="选择关键字类型">
					<el-option v-for="(item,index) in keywordTypeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>
			<span class="inputSpan">
				<el-input placeholder="请输入关键字" v-model="keyword"></el-input>
			</span>
			
			<span>
				<el-select v-model="gradeVal" placeholder="选择年级">
					<el-option v-for="(item,index) in gradeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>
			
			<span>
				<el-select v-model="classVal" placeholder="选择班级">
					<el-option v-for="(item,index) in classList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>
			
			<span>
				<el-date-picker v-model="value1" type="datetimerange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
				</el-date-picker>
			</span>

			


			<span class="searchButtonDiv">

				<el-button type="primary" @click="search()">
					搜索
				</el-button>

				<!--<el-button @click="export_db"  :loading="exportLoading" plain type="success">-->
				<!--导出-->
				<!--</el-button>-->
				<!-- <el-button type="success" plain @click="exportdata()">
					导出
				</el-button> -->
			</span>
		</div>

		<div class="contentDiv">

			<el-table :data="tableData" style="width: 100%;text-align: center" max-height="800">

				<el-table-column prop="_imgId" label="		图片id" align="center">
				</el-table-column>

				<el-table-column prop="_userid" label="		学生id" width="120" align="center">
				</el-table-column>
				<el-table-column prop="_fromId" label="		来源id" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_type" label="		类型" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_img_url" label="	图片" width="200" align="center">
					<template slot-scope="scope">
					
						<img :src="imgSrc+scope.row._img_url" style=" width:50px"/> 
					</template>
				</el-table-column>
				<el-table-column prop="_state" label="		状态" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_bind_activity" label="			赞助商" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_bind_place" label="		活动" width="200" align="center">
				</el-table-column>
				
				<el-table-column prop="_comment" label="	处理人" width="200" align="center">
				</el-table-column>
				
				<el-table-column prop="_handleAdmin" label="		处理人姓名" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_ces" label="		碳排放" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_name" label="		姓名" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_id" label="	id" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_acc" label="	账号" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_grade" label="	用户年级" width="200" align="center">
				</el-table-column>
				<el-table-column prop="_class" label="	用户班级" width="200" align="center">
				</el-table-column>
				<!-- 	<el-table-column label="查看凭证" align="center" width="120">
					<template slot-scope="scope">
						<el-button type='text' @click="dilangOpen(scope.row)" size="small">查看凭证</el-button>
					</template>
				</el-table-column> -->



				<!--<el-table-column align="center" label="操作">
					<template slot-scope="scope">
						<el-button type="text" size="small">审核</el-button>
						<div v-if="scope.row._status=='待审核'||scope.row._status=='未认证'">
							<el-button type="danger" @click="stateHandle(scope.row,'认证不通过')" plain size="small">不通过</el-button>
							<el-button type="primary" @click="stateHandle(scope.row,'认证通过')" plain size="small">通过</el-button>
						</div>

						<div v-else>
							<el-button type="default" disabled plain size="small">不可审核</el-button>
						</div>

					</template>
				</el-table-column>-->
				<!-- <el-table-column label="操作" align="center" width="120" fixed="right">
					<template slot-scope="scope">
						<el-button type='text' @click="update(scope.row)" size="small">更改订单状态</el-button>
					</template>
				</el-table-column> -->
			</el-table>
			<div class="block">

				<el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" layout="total,  prev, pager, next, jumper"
				 :total="listTotal">
				</el-pagination>
			</div>



		</div>
	</div>
</template>

<script>
	export default {
		name: "user",
		components: {},
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')

			console.log(this.admin, this.keys);
			this.screen();
		},
		data() {
			return {
				clientSize: true,
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				imgSrc:sessionStorage.getItem('img_cfg'),
				keyword: "",
				type: "",
				state: "",

				classVal:'全部',
				gradeVal:'全部',
				classList: [{
					value: '全部',
					label: '全部'
				}],
				gradeList: [{
					value: '全部',
					label: '全部'
				}],
				keywordTypeVal:'_userid',
				keywordTypeList:[
					{
					value: '_userid',
					label: '_userid'
					}, {
						value: '_name',
						label: '_name'
					}, {
						value: '_acc',
						label: '_acc'
					}, {
						value: '_check_admin',
						label: '_check_admin'
					}
				],
				page: 1,
				pageSize: 10,
				typeVal: '纸质垃圾',
				//在售/下架/全部
				typeList: [{
					value: '纸质垃圾',
					label: '纸质垃圾'
				}],
				list: [

				],
				tableData: [],
				listTotal: 0,
				exportLoading: false,
				checkLoading: false,
				value1: [new Date(2019, 3, 1, 10, 10), new Date(2020, 10, 11, 10, 10)]
			};
		},
		mounted() {
			
			this.initdata();
		},
		methods: {
			/* update(item){
				const _this = this;
				console.log('keyword', item);
				let data = {
					adminName: this.admin,
					keys: this.keys,
					_oid:item._oid,
				};
				
				this.axios.post(this.baseUrl + "/admin_orderHandle", data)
					.then(function(response) {
						console.log('response', response);
					
						_this.$toast.message(response.data.data);
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false
				
					});
			},
			exportdata() {
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					type: this.typeVal,
					keyword: this.keyword,
					_class: this.classVal,
					_grade: this.gradeVal,
				};

				this.axios.post(this.baseUrl + "/admin_userListExport", data, {
						responseType: 'blob'
					})
					.then(function(response) {
						console.log('response', response);
						const content = response.data;
						const blob = new Blob([content], {
							type: 'text/csv,charset=UTF-8'
						});
						const fileName = '学生列表.csv';
						if ('download' in document.createElement('a')) { // 非IE下载
							const elink = document.createElement('a');
							elink.download = fileName;
							elink.style.display = 'none';
							elink.href = URL.createObjectURL(blob);
							document.body.appendChild(elink);
							elink.click();
							URL.revokeObjectURL(elink.href); // 释放URL 对象
							document.body.removeChild(elink);
						} else { // IE10+下载
							navigator.msSaveBlob(blob, fileName);
						}
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			}, */
			initdata() {
				console.log(this.value1)
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					_type:this.typeVal,
					keywordType:this.keywordTypeVal,
					keyword: this.keyword,
					startTime: Math.ceil(new Date(this.value1[0]).getTime()/1000),
					endTime:  Math.ceil(new Date(this.value1[1]).getTime()/1000),
					page: this.page,
					pageSize: this.pageSize,
					_class:this.classVal,
					_grade:this.gradeVal,
				};


				this.axios.post(this.baseUrl + "/admin_coinList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 203 || response.data.result == 200) {
							_this.$toast.message('查询成功！');
							_this.tableData = response.data.list;
							_this.listTotal = response.data.count;
							console.log('_this.listTotal',_this.listTotal)
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},
			search() {
				this.initdata();
			},
			oneKeyPass() {
				this.$confirm('确定 一键通过 吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.checkLoading = true
					let _this = this
					let postData = new FormData();
					postData.append("keys", this.keys); // 用户id
					postData.append("admin", this.admin); //管理员名
					this.axios
						.post(this.baseUrl + "/User/idCheckOneKey", postData)
						.then(function(response) {
							if (response.data.result == 100) {
								_this.$toast.message('操作成功！');
								_this.list_db();

							} else {
								_this.$toast.message(response.data.data);
							}
							_this.checkLoading = false
						})
						.catch(function(error) {
							_this.$toast.message('网路错误！');
							console.log(error);
							_this.checkLoading = false

						});
				}).catch(() => {});
			},
			dilangOpen(row) {
				this.centerDialogVisible = true
				this.scopeRow = row
			},
			stateHandle(row, state) {

				this.$confirm('确定 ' + state + ' 吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.checkLoading = true
					let _this = this
					let postData = new FormData();
					postData.append("keys", this.keys); // 用户id
					postData.append("admin", this.admin); //管理员名
					postData.append("id", row._userid); //更改会员ID
					postData.append("state", state); //更改会员ID
					this.axios
						.post(this.baseUrl + "/User/idCheckHandle", postData)
						.then(function(response) {
							if (response.data.result == 100) {
								_this.$toast.message('操作成功！');
								_this.list_db();
								_this.centerDialogVisible = false
							} else {
								_this.$toast.message(response.data.data);
							}
							_this.checkLoading = false
						})
						.catch(function(error) {
							_this.$toast.message('网路错误！');
							console.log(error);
							_this.checkLoading = false

						});
				}).catch(() => {});
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.pageSize = val;
				this.list_db()

			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.page = val;
				this.initdata();
			},

			handleSortChange({
				name,
				order
			}) {
				this.list = this.list.sort(
					(a, b) => (order === "asc" ? a[name] - b[name] : b[name] - a[name])
				);
			},
			removeSelect(selectIndex) {
				const index = this.selects.indexOf(selectIndex);
				this.selects.splice(index, 1);
			},
			list_db() {
				let _this = this
				let loading = this.$loading()

				let postData = new FormData();
				postData.append("size", this.pageSize); //单页最大条数

				postData.append("page", this.page); //当前分页
				postData.append("keyword", this.keyword); //关键词 用户id
				postData.append("admin", this.admin); //管理员名
				postData.append("keys", this.keys); //管理员秘钥

				postData.append("type", this.type); //充值类型

				this.axios
					.post(this.baseUrl + "/User/idCardSearch", postData)
					.then(function(response) {
						if (response.data.result == 100) {

							_this.tableData = response.data.list

							_this.listTotal = response.data.page.total

						} else {
							_this.list = []
							_this.listTotal = 0

						}


						loading.close()
					})
					.catch(function(error) {
						_this.list = []
						_this.listTotal = 0

						console.log(error);
						loading.close()

					});
			},
			export_db() {
				this.exportLoading = true
				let _this = this
				let dateStart = this.timeArr === null ? 1 : this.timeArr[0].getTime() / 1000;
				let dateEnd = this.timeArr === null ? Math.ceil(new Date().getTime() / 1000) : this.timeArr[1].getTime() / 1000;
				let postData = new FormData();

				postData.append("start", dateStart); //开始时间
				postData.append("end", dateEnd); //结束时间

				postData.append("keyword", this.keyword); //关键词 用户id
				postData.append("admin", this.admin); //管理员名
				postData.append("keys", this.keys); //管理员秘钥
				postData.append("type", this.type); //充值类型
				this.axios
					.post(this.baseUrl + "Coin/export", postData, {
						responseType: 'blob'
					})
					.then(function(response) {
						var blob = new Blob([response.data])
						var downloadElement = document.createElement('a');
						var href = window.URL.createObjectURL(blob); //创建下载的链接
						downloadElement.href = href;
						downloadElement.download = _this.keyword + '提现数据.csv'; //下载后文件名
						document.body.appendChild(downloadElement);
						downloadElement.click(); //点击下载
						document.body.removeChild(downloadElement); //下载完成移除元素
						window.URL.revokeObjectURL(href); //释放掉blob对象

						_this.exportLoading = false
					})
					.catch(function(error) {
						_this.exportLoading = false
						console.log(error);
					});
			},
			screen() {
				this.page = 1;

				this.list_db()
			}
		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



