<template>
  <div class="hello">
        <div class="block">
            <el-date-picker
            v-model="timeArr"
            type="datetimerange"
            :picker-options="pickerOptions2"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            align="right">
            </el-date-picker>
        </div>

    <ul class="totalUl totalUl1">
      <p style="text-align: left;font-weight: bold;font-size: 18px;padding-left: 0.5%;margin-left: 10px">充值</p>

        <li>
            <p>当前充值：{{Math.ceil(payList['day']['dytcPay']==null ? 0:payList['day']['dytcPay'])}} DYTC</p>
            <p>总计充值：{{Math.ceil(payList['all']['dytcPay'])}} DYTC </p>
        </li>
        <li>
          <p>当前充值：{{payList['day']['ethPay']==null ? 0:payList['day']['ethPay']}} ETH</p>
          <p>总计充值：{{payList['all']['ethPay']}} ETH </p>
        </li>
    </ul>

      <ul class="totalUl">
        <p style="text-align: left;font-weight: bold;font-size: 18px;padding-left: 0.5%;margin-left: 10px">提现</p>

        <li>
          <p>当前提现：{{Math.ceil(withdrawList['day']['dytcWithdraw']==null ? 0:withdrawList['day']['dytcWithdraw'])}} DYTC</p>
          <p>总计提现：{{Math.ceil(withdrawList['all']['dytcWithdraw'])}} DYTC </p>
        </li>

        <li>
          <p>当前提现：{{withdrawList['day']['ethWithdraw']==null ? 0:withdrawList['day']['ethWithdraw']}} ETH</p>
          <p>总计提现：{{withdrawList['all']['ethWithdraw']}} ETH </p>
        </li>

    </ul>

    <ul class="totalUl totalUl3">
      <p style="text-align: left;font-weight: bold;font-size: 18px;padding-left: 0.5%;margin-left: 10px">会员 & 审核</p>
      <li>
        <p>新增会员：{{userList['userDay']==null ? 0:userList['userDay']}} 人</p>
        <p>全部会员：{{userList['userAll']}} 人 </p>
      </li>
      <li sytle="overflow:auto">
        <p>待审核会员：{{userList['check']['待审核']}} 人</p>
        <p>未认证会员：{{userList['check']['未认证']}} 人</p>

        <p>已通过会员：{{userList['check']['认证通过']}} 人</p>
        <p>未通过会员：{{userList['check']['认证不通过']}} 人</p>
      </li>



    </ul>

    <ul class="totalUl">
      <p style="text-align: left;font-weight: bold;font-size: 18px;padding-left: 0.5%;margin-left: 10px">产出</p>

      <li>
        <p>当前产出：{{Math.ceil(outList['out']==null ? 0:outList['out'])}} DYTC</p>
        <p>总计产出：{{Math.ceil(outList['outAll'])}} DYTC </p>
      </li>

    </ul>


        <!--<div class="block">-->
          <!--<span class="inputSpan"><el-input placeholder="输入ID或姓名查询资产" v-model="keyword"></el-input></span>-->
        <!--</div>-->
  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  components:{

  },
  mounted(){
    let allScreen=document.body.clientWidth

    let windowWidthHeader=allScreen-145
    let windowWidthLeft=allScreen-64


    let coll=sessionStorage.getItem('collapse')

    if(coll){

      $(".hello").css({'width':windowWidthHeader})


    }else{

      $(".hello").css({'width':windowWidthLeft})

    }
      this.admin=sessionStorage.getItem('admin')

      this.keys=sessionStorage.getItem('keys')
      this.getData()
  },
  data () {
    return {
        admin:'',
        keys:'',
        keyword:'',
        vipOut:true,
        list:{'out':0},
         pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        timeArr:[new Date(new Date().setHours(0, 0, 0, 0)),new Date()],
        payList: {
          day:{
            dytcPay: 0,
            ethPay: 0,
          },
          all:{
            dytcPay: 0,
            ethPay: 0,
          },
        },
        withdrawList: {
          day:{
            dytcWithdraw: 0,
            ethWithdraw: 0,
          },
          all:{
            dytcWithdraw: 0,
            ethWithdraw: 0,
          },
        },
        outList: {
        out:0,
        outAll:0,
      },
        userList:{
          userDay:0,
          userAll:0,
          check:{
            '未认证':0,
            '待审核':0,
            '认证通过':0,
            '认证不通过':0,
          }
        }
    }
  },
  methods:{
      getData(){
        let _this=this
        let loading = this.$loading()
        let dateStart = this.timeArr === null ? 1 : this.timeArr[0].getTime() / 1000;

        let dateEnd = this.timeArr === null ? Math.ceil(new Date().getTime() / 1000) : this.timeArr[1].getTime() / 1000;
        let postData = new FormData();
        postData.append("start", dateStart); // 充值金额
        postData.append("keys", this.keys); // 用户id
        postData.append("admin", this.admin); //管理员名
        postData.append("end", dateEnd); //更改会员ID

        //充值总计
        this.axios
        .post(this.baseUrl+"User/payTotal", postData)
        .then(function(response) {

          if(response.data.result==100){
              _this.payList=response.data.list
          }else{
              _this.payList= {
              day:{
                dytcPay: 0,
                ethPay: 0,
              },
              all:{
                dytcPay: 0,
                ethPay: 0,
              },
            }
          }
        })
        .catch(function(error) {
           _this.payList= {
            day:{
              dytcPay: 0,
              dytcEth: 0,
            },
            all:{
              dytcPay: 0,
              dytcEth: 0,
            },
          }

          console.log(error);
          loading.close()
        });



        //提现总计

        this.axios
          .post(this.baseUrl+"User/withdrawTotal", postData)
          .then(function(response) {

            if(response.data.result==100){
              _this.withdrawList=response.data.list
            }else{
              _this.withdrawList={
                day:{
                  dytcWithdraw: 0,
                  ethWithdraw: 0,
                },
                all:{
                  dytcWithdraw: 0,
                  ethWithdraw: 0,
                },
              }
            }
          })
          .catch(function(error) {
            _this.withdrawList={
              day:{
                dytcWithdraw: 0,
                  ethWithdraw: 0,
              },
              all:{
                dytcWithdraw: 0,
                  ethWithdraw: 0,
              },
            }

            console.log(error);
            loading.close()
          });


        //产出总计

        this.axios
          .post(this.baseUrl+"User/outTotal", postData)
          .then(function(response) {

            if(response.data.result==100){
              _this.outList=response.data.list
            }else{
              _this.outList={
                day:{
                  dytcWithdraw: 0,
                  ethWithdraw: 0,
                },
                all:{
                  dytcWithdraw: 0,
                  ethWithdraw: 0,
                },
              }
            }
            loading.close()
          })
          .catch(function(error) {
            _this.withdrawList={
              day:{
                dytcWithdraw: 0,
                ethWithdraw: 0,
              },
              all:{
                dytcWithdraw: 0,
                ethWithdraw: 0,
              },
            }

            console.log(error);
            loading.close()
          });

  // 会员统计

        this.axios
          .post(this.baseUrl+"User/userTotal", postData)
          .then(function(response) {
            console.log(response)
            if(response.data.result==100){

              let userList=response.data.list
              _this.userList.userDay=userList.userDay
              _this.userList.userAll=userList.userAll

              for(let i in _this.userList.check){
                for(let a in userList.check){
                  if(i==userList.check[a]._status){
                    _this.userList.check[i]=userList.check[a].total
                  }
                }
              }
              console.log(_this.userList)
            }else{
              _this.userList={
                  userDay:0,
                  userAll:0,
                  check:{
                  '未认证':0,
                  '待审核':0,
                  '认证通过':0,
                  '认证不通过':0,
                }
              }
            }
            loading.close()
          })
          .catch(function(error) {
            _this.userList={
              userDay:0,
              userAll:0,
              check:{
                '未认证':0,
                '待审核':0,
                '认证通过':0,
                '认证不通过':0,
              }
            }
            console.log(error);
            loading.close()
          });
      },

  },
  watch:{
      timeArr(){
          this.getData()
      }
  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}

a {
  color: #42b983;
}
.login{

}
.totalUl{
 width: 48%;
 margin:auto;
 margin:10px;
 overflow: hidden;
 float: left;
 /* background-color: #000; */
}
.totalUl>li{
    width:48%;
    height:120px;
    line-height: 60px;
    border-radius:3px;
    text-align: center;
    float: left;
    background-color: rgb(103,116,136);
    margin-left: 1%;
    padding:0 10px;
    font-size: 16px;
    color:#Fff;
  overflow: auto;
}
.totalUl>li>p:nth-child(1){
    border-bottom:1px solid #ddd;
}

.totalUl1>li:nth-child(2){
    margin-left: 0.5%;
    background-color: rgb(111,71,190);

}

.totalUl1>li:nth-child(3){
    background-color: rgb(111,71,190);
}

.totalUl3>li:nth-child(2){
    margin-left: 0.5%;
    background-color: rgb(21,105,195);

}

.totalUl3>li:nth-child(3){
  background-color: rgb(21,105,195);
}

/*.totalUl>li:nth-child(6){*/
    /*background-color: rgb(63,81,80);*/
/*}*/
/*.totalUl>li:nth-child(5){*/
    /*background-color: rgb(21,105,195);*/
    /*margin-left: 0.5%*/
/*}*/
  .totalUl p{
    text-align: left;
  }

</style>



