<template>
	<div class="user">
		<div class="searchDiv">

			<span>

				<el-select v-model="typeVal" placeholder="请选择类型">
					<el-option v-for="(item,index) in typeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>


			<span class="inputSpan">
				<el-input placeholder="请输入关键字" v-model="keyword"></el-input>
			</span>

			<span>
				<el-select v-model="gradeVal" placeholder="请选择年级">
					<el-option v-for="(item,index) in gradeList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>

			<span>
				<el-select v-model="classVal" placeholder="请选择班级">
					<el-option v-for="(item,index) in classList" :label="item.label" :key="index" :value="item.value"></el-option>
				</el-select>
			</span>


			<span class="searchButtonDiv">

				<el-button type="#default" @click="search()">
					搜索
				</el-button>

				<el-button type="default"  @click="exportdata()">
					导出
				</el-button>


					<el-button type="success" @click="onkeyOpen" plain>
						一键生成
					</el-button>

			</span>
		</div>

		<div class="contentDiv">

			<el-table :data="tableData" style="width: 100%;text-align: center" max-height="800">

				<el-table-column prop="_id" label="用户ID" align="center">
				</el-table-column>

				<el-table-column prop="_acc" show-overflow-tooltip label="账号" align="center">
				</el-table-column>
				<el-table-column prop="_name" show-overflow-tooltip label="姓名" align="center">
				</el-table-column>
				<el-table-column prop="_pwd" label="密码" align="center">
				</el-table-column>
				<el-table-column prop="_grade" label="年级" align="center">
				</el-table-column>
				<el-table-column prop="_class" label="班级" align="center">
				</el-table-column>
				<el-table-column prop="_coin_getId" label="	积分获取id" align="center">
				</el-table-column>
				<el-table-column prop="_coin" label="	积分" align="center">
				</el-table-column>
				<el-table-column prop="_batch" label="索引码" align="center">
				</el-table-column>
				<el-table-column prop="_type" label="	账号类别" align="center">
				</el-table-column>
				
				<el-table-column label="审核" align="center" fixed="right">
						<template slot-scope="scope">
							<!--<el-button type='text' size="small">审核</el-button>-->
						<el-button type='text' @click="dilangOpen(scope.row,'修改')" size="small" >修改</el-button>
						<el-button type='text' @click="stateHandle1(scope.row,'删除')" size="small" >删除</el-button>
						<el-button type='text' @click="addHandle(scope.row,'加分')" size="small" v-if="scope.row._type==='学生'" >加分</el-button>
						</template>
					</el-table-column>

			</el-table>

			<div class="block">

				<el-pagination background @size-change="handleSizeChange"  @current-change="handleCurrentChange" layout="total,  prev, pager, next, jumper"
				 :total="listTotal">
				</el-pagination>
			</div>
			<el-dialog :title="action+'学生信息'" :visible.sync="centerDialogVisible" width="800px" center>
				
				<el-form ref="infoForm" :model="infoForm"  label-width="120px">
				
					<el-form-item label="年级" class="colorB" prop="_tittle">
						<el-input v-model="infoForm._userHandleRow._grade"></el-input>
					</el-form-item>
				
					<el-form-item label="班级" class="colorB" prop="_detail">
						<el-input v-model="infoForm._userHandleRow._class"></el-input>
					</el-form-item>
					
					<el-form-item label="姓名" class="colorB" prop="_weight">
						<el-input v-model="infoForm._userHandleRow._name"></el-input>
					</el-form-item>
					
					<el-form-item label="账号" class="colorB" prop="_weight">
						<el-input v-model="infoForm._userHandleRow._acc"></el-input>
					</el-form-item>
						
					<el-form-item label="密码" class="colorB" prop="_weight">
							<el-input v-model="infoForm._userHandleRow._pwd"></el-input>
					</el-form-item>
					
				</el-form>
				
				<span slot="footer" class="dialog-footer">
					<el-button  @click="stateHandle(scopeRow)" :loading="checkLoading" plain size="small">提交</el-button>
				</span>
				
			</el-dialog>

      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible_onekey"
        width="60%"
        center>
        <create></create>

      </el-dialog>


	  
      <el-dialog
        title="加减分操作"
        :visible.sync="addFlag"
        width="60%"
        center>
       <el-form ref="form"  label-width="80px">		
			<el-form-item label="分数操作">
				<el-input-number v-model="num" :precision="2" :step="0.1" :max="2" :min="-1"></el-input-number>
			</el-form-item> 

			<el-form-item>
				<el-button type="primary" @click=" cancelAdd ">取消</el-button>
				<el-button @click="addNum">确定</el-button>
			</el-form-item>

		</el-form>
      </el-dialog>

		</div>
	</div>
</template>

<script>
  import create from './create.vue'

	export default {
		name: "user",
		components: {
      create
    },
		mounted() {
			if (document.body.clientWidth < 800) {
				this.clientSize = false
			}
			this.admin = sessionStorage.getItem('admin')
			this.keys = sessionStorage.getItem('keys')
			this.img_cfg = sessionStorage.getItem('img_cfg')

			console.log('params',this.$route.params.id);
			this.screen();
		},
		data() {
			return {
				addFlag:false,
				num:0,
				listRow:null,
				clientSize: true,
				selects: [],
				admin: sessionStorage.getItem('admin'),
				keys: sessionStorage.getItem('keys'),
				power: sessionStorage.getItem('power'),
				img_cfg: sessionStorage.getItem('img_cfg'),
				timeArr: null,
				keyword: "",
				type: "",
				state: "",
				class: '',
				grade: '',
				page: 1,
				pageSize: 10,
				action:'',
				typeVal: '_name',
				gradeVal: '全部',
				classVal: '全部',
				classList: [{
					value: '全部',
					label: '全部'
				}, {
					value: '1班',
					label: '1班'
				}, {
					value: '2班',
					label: '2班'
				}, {
					value: '3班',
					label: '3班'
				}, {
					value: '4班',
					label: '4班'
				}],
				gradeList: [{
					value: '全部',
					label: '全部'
				}, {
					value: '预备年级',
					label: '预备年级'
				}, {
					value: '七年级',
					label: '七年级'
				}, {
					value: '八年级',
					label: '八年级'
				}, {
					value: '九年级',
					label: '九年级'
				}],
				typeList: [{
					value: '_name',
					label: '姓名'
				}, {
					value: '_acc',
					label: '账号'
				}, {
					value: '_id',
					label: '学生id'
				}, {
					value: '_batch',
					label: '索引码'
				}],
				list: [

				],
				tableData: [],
				listTotal: 0,
				exportLoading: false,
				flykAll: 0,
				ethAll: 0,
				ethService: 0,
				flykService: 0,
				ethOk: 0,
				flykOk: 0,
				centerDialogVisible: false,
				centerDialogVisible_onekey: false,
				scopeRow: {},
				checkLoading: false,
				infoForm:{
					adminName:sessionStorage.getItem('admin'),
					keys:sessionStorage.getItem('keys'),
					_userid:'',
					_userHandleRow:{
						_name:'',
						_pawd:'',
						_class:'',
						_grade:'',
						_acc:''
					},
					
					
				},
			};
		},
		mounted() {
			this.initdata();
		},
		methods: {
			cancelAdd(){
				this.addFlag = false;
			},
			addHandle(row){
				console.log("row",row);
				this.addFlag = true;
				this.listRow = row;
			},
			addNum(){
				const _this = this;
				let data = {
					adminName: this.admin,
					keys: this.keys,
					value:this.num,
					id:this.listRow._id,
				};
				if(this.num == 0){
					return;
				};

				this.axios.post(this.baseUrl + "/addnum", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('操作成功！');
							_this.addFlag = false;
							_this.initdata();

						} else {
							_this.$toast.message(response.data.data);
						}
						
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						

					});

			},

			onkeyOpen(){
				this.centerDialogVisible_onekey=true
			},
			exportdata() {
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					type: this.typeVal,
					keyword: this.keyword,
					_class: this.classVal,
					_grade: this.gradeVal,
				};

				this.axios.post(this.baseUrl + "/admin_userListExport", data,{
						responseType: 'blob'
					})
					.then(function(response) {
						console.log('response',response);
						const content = response.data;
						const blob = new Blob([content], {
							type: 'text/csv,charset=UTF-8'
						});
						let describe =  (_this.gradeVal +_this.classVal) == '全部全部'?'所有学生':_this.gradeVal +_this.classVal;
						
						const fileName = describe+'列表-'+new Date()+'.csv';
						if ('download' in document.createElement('a')) { // 非IE下载
							const elink = document.createElement('a');
							elink.download = fileName;
							elink.style.display = 'none';
							elink.href = URL.createObjectURL(blob);
							document.body.appendChild(elink);
							elink.click();
							URL.revokeObjectURL(elink.href); // 释放URL 对象
							document.body.removeChild(elink);
						} else { // IE10+下载
							navigator.msSaveBlob(blob, fileName);
						}
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},
			initdata() {
				const _this = this;
				console.log('keyword', this.keyword)
				let data = {
					adminName: this.admin,
					keys: this.keys,
					type: this.typeVal,
					keyword: this.keyword,
					_class: this.classVal,
					_grade: this.gradeVal,
					page: this.page,
					pageSize: this.pageSize
				};


				this.axios.post(this.baseUrl + "/admin_userList", data)
					.then(function(response) {
						console.log(response);
						if (response.data.result == 200) {
							_this.$toast.message('查询成功！');
							_this.tableData = response.data.list;
							_this.listTotal = response.data.count;
						} else {
							_this.$toast.message(response.data.data);
						}
						_this.checkLoading = false
					})
					.catch(function(error) {
						_this.$toast.message('网路错误！');
						console.log(error);
						_this.checkLoading = false

					});
			},
			search() {
				this.initdata();
			},
			oneKeyPass() {
				this.$confirm('确定 一键通过 吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.checkLoading = true
					let _this = this
					let postData = new FormData();
					postData.append("keys", this.keys); // 用户id
					postData.append("admin", this.admin); //管理员名
					this.axios
						.post(this.baseUrl + "/User/idCheckOneKey", postData)
						.then(function(response) {
							if (response.data.result == 100) {
								_this.$toast.message('操作成功！');
								_this.list_db();

							} else {
								_this.$toast.message(response.data.data);
							}
							_this.checkLoading = false
						})
						.catch(function(error) {
							_this.$toast.message('网路错误！');
							console.log(error);
							_this.checkLoading = false

						});
				}).catch(() => {});
			},
			dilangOpen(row,state) {
				this.action = state;
				this.centerDialogVisible = true
				this.scopeRow = row;
				console.log(row);
				this.infoForm._userid = row._id;
				this.infoForm._userHandleRow = {
					_name:row._name,
					_pwd:row._pwd,
					_class:row._class,
					_grade:row._grade,
					_acc:row._acc
				};

				
			},
			stateHandle(row,) {
				const _this = this;
				if(_this.action == '修改'){
					this.infoForm._userHandleRow = JSON.stringify(this.infoForm._userHandleRow);
					this.axios
						.post(this.baseUrl + "/admin_userHandle", _this.infoForm)
						.then(function(response) {
							console.log(response)
							if (response.data.result ==200) {
								_this.centerDialogVisible =false;
								_this.$toast.message(response.data.data);
								_this.initdata();
							} else {
					
							}
						})
						.catch(function(error) {
							console.log(error);					
						});
				};
			},
			stateHandle1(row,) {
				const _this  = this;
					console.log(row);
					const data = {
						adminName:sessionStorage.getItem('admin'),
						keys:sessionStorage.getItem('keys'),
						_userid:row._id,
					};
					this.$confirm('确定删除吗？', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						
						this.axios
							.post(this.baseUrl + "/admin_userDel", data)
							.then(function(response) {
								console.log(response)
								if (response.data.result == 200) {
									_this.$toast.message('删除成功！');
									_this.centerDialogVisible = false;
									_this.initdata();
								} else {
									_this.$toast.message(response.data.data);
								}
								_this.checkLoading = false
							})
							.catch(function(error) {
								_this.$toast.message('网路错误！');
								console.log(error);
								_this.checkLoading = false
					
							});
					}).catch(() => {});
			},
			handleSizeChange(val) {
				// console.log(`每页 ${val} 条`);
				this.pageSize = val;
				this.list_db()

			},
			handleCurrentChange(val) {
				// console.log(`当前页: ${val}`);
				this.page = val;
				this.initdata();
			},
		
			handleSortChange({
				name,
				order
			}) {
				this.list = this.list.sort(
					(a, b) => (order === "asc" ? a[name] - b[name] : b[name] - a[name])
				);
			},
			removeSelect(selectIndex) {
				const index = this.selects.indexOf(selectIndex);
				this.selects.splice(index, 1);
			},
			list_db() {
				let _this = this
				let loading = this.$loading()

				let postData = new FormData();
				postData.append("size", this.pageSize); //单页最大条数

				postData.append("page", this.page); //当前分页
				postData.append("keyword", this.keyword); //关键词 用户id
				postData.append("admin", this.admin); //管理员名
				postData.append("keys", this.keys); //管理员秘钥

				postData.append("type", this.type); //充值类型

				this.axios
					.post(this.baseUrl + "/User/idCardSearch", postData)
					.then(function(response) {
						if (response.data.result == 100) {

							_this.tableData = response.data.list

							_this.listTotal = response.data.page.total

						} else {
							_this.list = []
							_this.listTotal = 0

						}


						loading.close()
					})
					.catch(function(error) {
						_this.list = []
						_this.listTotal = 0

						console.log(error);
						loading.close()

					});
			},
			export_db() {
				this.exportLoading = true
				let _this = this
				let dateStart = this.timeArr === null ? 1 : this.timeArr[0].getTime() / 1000;
				let dateEnd = this.timeArr === null ? Math.ceil(new Date().getTime() / 1000) : this.timeArr[1].getTime() / 1000;
				let postData = new FormData();

				postData.append("start", dateStart); //开始时间
				postData.append("end", dateEnd); //结束时间

				postData.append("keyword", this.keyword); //关键词 用户id
				postData.append("admin", this.admin); //管理员名
				postData.append("keys", this.keys); //管理员秘钥
				postData.append("type", this.type); //充值类型
				this.axios
					.post(this.baseUrl + "Coin/export", postData, {
						responseType: 'blob'
					})
					.then(function(response) {
						var blob = new Blob([response.data])
						var downloadElement = document.createElement('a');
						var href = window.URL.createObjectURL(blob); //创建下载的链接
						downloadElement.href = href;
						downloadElement.download = _this.keyword + '提现数据.csv'; //下载后文件名
						document.body.appendChild(downloadElement);
						downloadElement.click(); //点击下载
						document.body.removeChild(downloadElement); //下载完成移除元素
						window.URL.revokeObjectURL(href); //释放掉blob对象

						_this.exportLoading = false
					})
					.catch(function(error) {
						_this.exportLoading = false
						console.log(error);
					});
			},
			screen() {
				this.page = 1;

				this.list_db()
			}
		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.user {}

	.searchDiv,
	.contentDiv {
		border-radius: 1px;
		box-shadow: 1px 1px 1px #eee;

		background-color: #fff;
		padding: 10px;
		text-align: left;
		overflow: hidden;
		margin-top: 10px;
	}

	.searchDiv>span {
		display: block;
		float: left;
		margin: 10px;
	}

	.searchButtonDiv {
		height: 56px;
		padding: 0px 0;
		overflow: hidden;
	}

	.tablePaper {
		width: 100%;
	}

	.totalbtn {
		margin: 10px
	}

	.tablePaper {
		width: 100%;
		padding-bottom: 10px;
	}

	.block {

		text-align: center
	}

	.demo-table-expand {
		font-size: 0;
	}

	.demo-table-expand label {
		width: 90px;
		color: #99a9bf;
	}

	.demo-table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 46%;
		text-align: left;
	}
</style>



